<template>
  <footer>
    <div class="line"></div>
    <div class="container-fluid my-container">
      <div class="box one">
        <div class="row">
          <div class="col-md col-sm-12 col-12">
            <img src="static/imgs/iopet_copyright.svg" alt="" class="logo">
          </div>
          <div class="col-md col-sm-12 col-12 text-center">
            <p class="t1">
              <img class="ico2" src="static/imgs/ic_phone.svg" alt="">
              4001-4000
            </p>
            <p class="t2">
              <img class="ico2" src="static/imgs/ic_email.svg" alt="">
              contato@iopet.com.br
            </p>
          </div>
          <div class="col-md col-sm-12 col-12 text-right">
            <img src="static/imgs/ic_facebook.svg" alt="" class="ico">
            <img src="static/imgs/ic_instagram.svg" alt="" class="ico">
          </div>
        </div>
      </div>
      <div class="box two">
        <div class="row text-center">
          <div class="col">
            <p class="pt">Rua das Esmeraldas, 222, Bairro das Flores, São Paulo - SP</p>
          </div>
        </div>
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  name: '',
  data () {
    return {

    }
  }
}
</script>

<style scoped>
  footer{
    width: 100%;
    height: auto;
    background-color: #EFEEED;
    position: relative;
  }

  .line{
    width: 100%;
    height: 2px;
    display: block;
    background-color: #fff;
    position: absolute;
    left: 0;
    bottom: 80px;
  }

  .box{
    max-width: 1100px;
    display: block;
    margin: 0 auto;
  }

  .box.one{
    padding-top: 40px;
    padding-bottom: 40px;
    color: #89898B;
    position: relative;
  }

  .box.two{
    padding-top: 20px;
    padding-bottom: 20px;
    color: #5050507e;
  }

  .logo{
    height: 60px;
    width: auto;
  }

  .ico{
    display: inline-block;
    width: 30px;
    height: 30px;
    margin: 30px 5px 5px 0;
    cursor: pointer;
  }

  .ico2{
    display: inline-block;
    width: auto;
    height: 14px;
    margin: 5px;
  }

  .t1{
    margin-top: 15px;
    margin-bottom: 0;
  }

  @media only screen and (max-width: 992px){
    .my-container{
      max-width: 850px;
    }
  }

  @media only screen and (max-width: 767.98px){
    .logo{
      margin: 0 auto;
      display: block;
    }

    .text-right{
      text-align: center !important;
    }
  }
</style>



