<template>
  <div class="page">
    <app-client-header></app-client-header>
    <div class="container-fluid my-container">
      <div class="row">
        <div class="col-md col-sm-12 col-12">
          <app-doubts></app-doubts>
        </div>
        <div class="col-md col-sm-12 col-12">
          <p class="title">Ainda tem dúvidas?</p>
          <form @submit.prevent="submit">
            <div class="row">
              <div class="col-12">
                <label for="">Nome</label>
                <app-text-input :data="formData.name" className="wbg"></app-text-input>
              </div>
              <div class="col-12">
                <label for="">E-mail</label>
                <app-email-input :data="formData.email" className="wbg"></app-email-input>
              </div>
              <div class="col-12">
                <label for="">Mensagem</label>
                <app-textarea-input :data="formData.message" className="wbg"></app-textarea-input>
              </div>
              <div class="col-12">
                <button type="submit" class="btn btn-send">Enviar</button>
              </div>
            </div>
          </form>
          <div class="row mt30">
            <div class="col text-right">
              <p class="contact">
                <img src="static/imgs/ic_phone2.svg" alt="" class="phone">
                4001-4000
              </p>
              <p class="contact">
                <img src="static/imgs/ic_email2.svg" alt="" class="mail">
                contato@iopet.com.br
              </p>
              <p class="hours">Horário de atendimento das 8h às 18h</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import TextInput from '@/components/inputs/TextInput'
import EmailInput from '@/components/inputs/EmailInput'
import TextareaInput from '@/components/inputs/TextareaInput'
import EventBus from '@/main'
import Footer from '@/components/footer/Footer'
import ClientHeader from '@/components/header/ClientHeader'
import Doubts from '@/components/doubts/Doubts'
export default {
  name: 'DoubtsPage',
  data () {
    return {
      user: {},
      formData: {
        email: {
          error: {
            error: false,
            msg: 'Campo obrigatório'
          },
          model: ''
        },
        name: {
          error: {
            error: false,
            msg: 'Campo obrigatório'
          },
          model: ''
        },
        message: {
          error: {
            error: false,
            msg: 'Campo obrigatório'
          },
          model: ''
        },
      },
      questions: []
    }
  },
  components: {
    appFooter: Footer,
    appClientHeader: ClientHeader,
    appDoubts: Doubts,
    appTextInput: TextInput,
    appEmailInput: EmailInput,
    appTextareaInput: TextareaInput
  },
  methods: {
    validate () {
      if(this.formData.email.model === ''){
        this.formData.email.error.error = true
      }
      if(this.formData.name.model === ''){
        this.formData.name.error.error = true
      }
      if(this.formData.message.model === ''){
        this.formData.message.error.error = true
      }
      let error = false
      for(let key in this.formData){
        if(this.formData[key].error.error === true)
          error = true
      }
      return error
    },
    submit () {
      const error = this.validate()
      if(!error){
        console.log('Send e-mail')
      }
    }
  },
  created () {
    this.$store.dispatch('fetchUser').then(() =>{
      this.user = JSON.parse(localStorage.getItem('user'))
      EventBus.$emit('loadQuestions')
    })
  }
}
</script>

<style scoped>

  .page{
    margin-top: 130px;
  }

  .container-fluid{
    min-height: 100vh;
    max-width: 1080px;
  }

  .title{
    color: #E60965;
    font-size: 20px;
    font-family: ubuntu-medium;
    margin-bottom: 20px;
    margin-top: 15px;
  }

  label{
    color: #505050;
  }

  .contact{
    color: #1E62AC;
    font-size: 16px;
    font-family: Lato-bold;
    margin-bottom: 5px;
  }

  .phone{
    height: 15px;
    width: auto;
    margin-right: 5px;
  }

  .mail{
    height: 10px;
    width: auto;
    margin-right: 5px;
  }

  .hours{
    color: #89898B;
    font-size: 12px;
  }

  @media only screen and (max-width: 992px){
    .contact{
      font-size: 14px;
    }

    .hours{
      font-size: 11px;
    }
  }
</style>



