<template>
  <div class="input-content">
    <div @click="data.error.error = false"
         class="file-input"
         :class="[className, { error: data.error.error}]">
         <label :for="elId"><span class="btn btn-send">procurar</span></label>
         <div class="file">{{file}}</div>
    </div>
    <input
      :id="elId"
      type="file"
      ref="fileInput"
      @change="updated">
    <transition name="fade" mode="out-in">
      <span v-if="data.error.error" class="error-msg">{{data.error.msg}}</span>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'FileInput',
  data () {
    return {
      file: ''
    }
  },
  props: [
    'data',
    'placeholder',
    'className',
    'elId'
  ],
  methods: {
    updated (e) {
      this.$emit('input',
        this.data.model = e.target.files[0]
      )
      this.file = this.data.model.name
    }
  }
}
</script>

<style scoped>
  .input-content{
    position: relative;
  }

  .input-content input{
    display: none;
  }

  .input-content label{
    cursor: pointer;
  }

  .error-msg{
    bottom: -17px;
  }

  .btn{
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
  }

  .file{
    float: left;
  }
</style>



