<template>
  <div class="page">
    <app-quotation-header></app-quotation-header>
    <div v-if="overlay" class="overlay"></div>
    <transition name="fade" mode="out-in">
      <div v-if="modal" class="my-modal text-center">
        <p class="title">Seu plano foi cadastrado com sucesso!</p>
        <p class="msg">{{modalMsg}}</p>
        <button @click="replace()" class="btn btn-black">ok</button>
      </div>
    </transition>
    <div class="container-fluid my-container">
      <div class="row">
        <div class="col">
          <p class="title">O plano para o seu bichinho será de: <span class="desk">R$ {{numberFormat(quotation/100, 2, ',', '.')}}</span></p>
          <p class="title mob"><span>R$ {{numberFormat(quotation/100, 2, ',', '.')}}</span></p>
          <p v-if="auth" class="title">ao adicionar este animal, o valor passará a ser de <span class="desk">R$ {{numberFormat(planValue/100, 2, ',', '.')}}</span></p>
          <p v-if="auth" class="title mob"><span>R$ {{numberFormat(planValue/100, 2, ',', '.')}}</span></p>
          <p class="sub-title">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin id cursus sapien, ut egestas felis. Quisque pulvinar aliquet lectus at lacinia. Nam in dignissim lorem.</p>
        </div>
      </div>
      <form @submit.prevent="submit">
        <div class="row">
          <div class="col">
            <div class="center">
              <p class="box-title">Gostou do valor? Para finalizar o cadastro complete os dados abaixo.</p>
              <div class="box mb30">
                <div class="row">
                  <div class="col-12 text-center">
                    <div class="img-pet">
                      <img :src="(pet.img.model) ? previewImg : 'static/imgs/default_pet.svg'" alt="" class="img">
                      <input @change="petPicture" type="file" id="pic">
                      <label for="pic"> <img src="static/imgs/camera.svg" alt="" class="camera"></label>
                    </div>
                  </div>
                  <div class="col-12">
                    <label for="">Nome do animal</label>
                    <app-text-input className="wbg" v-model="pet.name.model" :data="pet.name"></app-text-input>
                  </div>
                  <div class="col-md-5 col-sm-6 col-6">
                    <label for="">Cor da pelagem</label>
                    <app-text-input className="wbg" v-model="pet.color.model" :data="pet.color"></app-text-input>
                  </div>
                  <div class="col-md-4 col-sm-6 col-6">
                    <label for="">Tem pedigree?</label>
                    <app-select-input elId="pedigree" v-model="pet.pedigree.model" :options="[{name:'Sim', value:1},{name:'Não', value:0}]" placeholder="Selecione" :data="pet.pedigree"></app-select-input>
                  </div>
                </div>
              </div>
              <div v-if="!auth" class="box">
                <div class="row mt30">
                  <div class="col-md-8 col-sm-12 col-12">
                    <label for="">Seu nome completo</label>
                    <app-text-input className="wbg" v-model="formUser.name.model" :data="formUser.name"></app-text-input>
                  </div>
                  <div class="col-md-4 col-sm-6 col-6">
                    <label for="">CPF</label>
                    <app-text-input elId="cpf" mask="cpf" className="wbg" v-model="formUser.cpf.model" :data="formUser.cpf"></app-text-input>
                  </div>
                  <div class="col-md-4 col-sm-6 col-6">
                    <label for="">E-mail</label>
                    <app-email-input className="wbg" v-model="formUser.email.model" :data="formUser.email"></app-email-input>
                  </div>
                  <div class="col-md-4 col-sm-6 col-6">
                    <label for="">Data de nascimento</label>
                    <app-text-input elId="birth" mask="date" className="wbg" v-model="formUser.birth.model" :data="formUser.birth"></app-text-input>
                  </div>
                  <div class="col-md-4 col-sm-6 col-6">
                    <label for="">Telefone</label>
                    <app-text-input className="wbg" v-model="formUser.phone.model" :data="formUser.phone"></app-text-input>
                  </div>
                  <div class="col-md-6 col-sm-6 col-6">
                    <label for="">Senha</label>
                    <app-password-input className="wbg" v-model="formUser.password.model" :data="formUser.password"></app-password-input>
                  </div>
                  <div class="col-md-6 col-sm-6 col-6">
                    <label for="">Confirmar senha</label>
                    <app-password-input className="wbg" v-model="formUser.confPassword.model" :data="formUser.confPassword"></app-password-input>
                  </div>
                  <div class="col-md-9 col-sm-6 col-6">
                    <label for="">CEP</label>
                    <app-text-input mask="cep" elId="cep" className="wbg" v-model="formUser.cep.model" :data="formUser.cep"></app-text-input>
                  </div>
                  <div class="col-md-3 col-sm-6 col-6">
                    <label for="">Nº</label>
                    <app-text-input elId="addressNumber" mask="addressNumber" className="wbg" v-model="formUser.number.model" :data="formUser.number"></app-text-input>
                  </div>
                  <div class="col-12">
                    <label for="">Rua</label>
                    <app-text-input className="wbg" v-model="formUser.street.model" :data="formUser.street"></app-text-input>
                  </div>
                  <div class="col-12">
                    <label for="">Bairro</label>
                    <app-text-input className="wbg" v-model="formUser.neighborhood.model" :data="formUser.neighborhood"></app-text-input>
                  </div>
                  <div class="col-12">
                    <label for="">Complemento</label>
                    <app-text-input className="wbg" v-model="formUser.complement.model" :data="formUser.complement"></app-text-input>
                  </div>
                  <div class="col-md-9 col-sm-6 col-6">
                    <label for="">Cidade</label>
                    <app-text-input className="wbg" v-model="formUser.city.model" :data="formUser.city"></app-text-input>
                  </div>
                  <div class="col-md-3 col-sm-6 col-6">
                    <label for="">UF</label>
                    <app-select-input elId="uf" v-model="formUser.uf.model" :options="stateList" placeholder="UF" :data="formUser.uf"></app-select-input>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-12 text-center">
                  <button type="submit" class="btn btn-new-pet mt30">cadastrar</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from '@/auth'
import globalAxios from 'axios'
import EventBus from '@/main'
import PasswordInput from '@/components/inputs/PasswordInput'
import TextInput from '@/components/inputs/TextInput'
import EmailInput from '@/components/inputs/EmailInput'
import SelectInput from '@/components/inputs/SelectInput'
import QuotationHeader from '@/components/header/QuotationHeader'
export default {
  name: 'PlanQuotationPage',
  data () {
    return {
      previewImg: null,
      firstStepRegister: {},
      planValue: null,
      overlay: false,
      modal: false,
      modalMsg: '',
      user: {},
      quotation: null,
      stateList: [
        {name: 'AC', value: 'AC'},
        {name: 'AL', value: 'AL'},
        {name: 'AP', value: 'AP'},
        {name: 'AM', value: 'AM'},
        {name: 'BA', value: 'BA'},
        {name: 'CE', value: 'CE'},
        {name: 'DF', value: 'DF'},
        {name: 'ES', value: 'ES'},
        {name: 'GO', value: 'GO'},
        {name: 'MA', value: 'MA'},
        {name: 'MT', value: 'MT'},
        {name: 'MS', value: 'MS'},
        {name: 'MG', value: 'MG'},
        {name: 'PA', value: 'PA'},
        {name: 'PB', value: 'PB'},
        {name: 'PR', value: 'PR'},
        {name: 'PE', value: 'PE'},
        {name: 'PI', value: 'PI'},
        {name: 'RJ', value: 'RJ'},
        {name: 'RN', value: 'RN'},
        {name: 'RS', value: 'RS'},
        {name: 'RO', value: 'RO'},
        {name: 'RR', value: 'RR'},
        {name: 'SC', value: 'SC'},
        {name: 'SP', value: 'SP'},
        {name: 'SE', value: 'SE'},
        {name: 'TO', value: 'TO'}
      ],
      pet: {
        name: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        img: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        color: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        pedigree: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: null
        }
      },
      formUser: {
        name: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        birth: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        password: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        confPassword: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        cpf: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        email: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        phone: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        street: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        number: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        cep: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        neighborhood: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        city: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        complement: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        uf: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        cep: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
      }
    }
  },
  components: {
    appQuotationHeader: QuotationHeader,
    appTextInput: TextInput,
    appSelectInput: SelectInput,
    appEmailInput: EmailInput,
    appPasswordInput: PasswordInput
  },
  computed: {
    auth() {
      return EventBus.isAuthenticated()
    }
  },
  methods: {
    replace () {
      if(EventBus.isAuthenticated()){
        this.$router.push({name: 'ClientPage'})
      }
      else{
        this.$router.push({name: 'IndexPage'})
      }
    },
    validate () {
      let error = false
      if(this.pet.name.model === ''){
        this.pet.name.error.error = true
      }
      if(this.pet.color.model === ''){
        this.pet.color.error.error = true
      }
      if(this.pet.pedigree.model === null){
        this.pet.pedigree.error.error = true
      }
      for(let key in this.pet){
        if(this.pet[key].error.erro === true){
          error = true
        }
      }
      if(!EventBus.isAuthenticated()){
        if(this.formUser.name.model === ''){
          this.formUser.name.error.error = true
        }
        if(this.formUser.password.model === ''){
          this.formUser.password.error.error = true
        }
        else if(this.formUser.password.model !== this.formUser.confPassword.model){
          this.formUser.password.error.error = true
          this.formUser.password.error.msg = 'As senhas não conferem'
          this.formUser.confPassword.error.error = true
          this.formUser.confPassword.error.msg = 'As senhas não conferem'
        }
        else if(this.formUser.password.model.length < 6){
          this.formUser.password.error.error = true
          this.formUser.password.error.msg = 'Mínimo 6 digitos'
          this.formUser.confPassword.error.error = true
          this.formUser.confPassword.error.msg = 'Mínimo 6 digitos'
        }
        if(this.formUser.confPassword.model === ''){
          this.formUser.confPassword.error.error = true
        }
        if(this.formUser.cpf.model === ''){
          this.formUser.cpf.error.error = true
        }
        else if(this.formUser.cpf.model < 14){
          this.formUser.cpf.error.error = true
          this.formUser.cpf.error.msg = 'CPF incorreto'
        }
        if(this.formUser.email.model === ''){
          this.formUser.email.error.error = true
        }
        if(this.formUser.birth.model === ''){
          this.formUser.birth.error.error = true
        }
        else if(this.formUser.birth.model < 10){
          this.formUser.birth.error.error = true
          this.formUser.birth.error.msg = 'Data inválida'
        }
        if(this.formUser.phone.model === ''){
          this.formUser.phone.error.error = true
        }
        if(this.formUser.street.model === ''){
          this.formUser.street.error.error = true
        }
        if(this.formUser.number.model === ''){
          this.formUser.number.error.error = true
        }
        if(this.formUser.cep.model === ''){
          this.formUser.cep.error.error = true
        }
        else if(this.formUser.cep.model < 10){
          this.formUser.cep.error.error = true
          this.formUser.cep.error.msg = 'CEP inválido'
        }
        if(this.formUser.neighborhood.model === ''){
          this.formUser.neighborhood.error.error = true
        }
        if(this.formUser.city.model === ''){
          this.formUser.city.error.error = true
        }
        if(this.formUser.uf.model === ''){
          this.formUser.uf.error.error = true
        }
        if(this.formUser.complement.model === ''){
          this.formUser.complement.error.error = true
        }
        for(let key in this.formUser){
          if(this.formUser[key].error.error === true){
            error = true
          }
        }
      }
      return error
    },
    petPicture (e) {
      this.pet.img.model = e.target.files[0]
      let reader = new FileReader()
      reader.onloadend = ( () => {
        this.previewImg = reader.result
      })
      if(e.target.files[0]){
        reader.readAsDataURL(e.target.files[0])
      }
    },
    submit () {
      let error = this.validate()
      if(!error) {
        if(!EventBus.isAuthenticated()){
          let formData = new FormData()
          formData.set('data_nascimento', this.firstStepRegister.data_nascimento)
          formData.set('especie_id', this.firstStepRegister.especie_id)
          formData.set('peso', this.firstStepRegister.peso)
          formData.set('porte_id', this.firstStepRegister.porte_id)
          formData.set('problema_saude', this.firstStepRegister.problema_saude)
          formData.set('raca_id', this.firstStepRegister.raca_id)
          formData.set('resgatado', this.firstStepRegister.resgatado)
          formData.set('sexo', this.firstStepRegister.sexo)
          formData.set('vacinacao_emdia', this.firstStepRegister.vacinacao_emdia)
          formData.set('carteira_vacinacao', this.firstStepRegister.carteira_vacinacao)
          formData.set('necessidade_especial', this.firstStepRegister.necessidade_especial)
          formData.set('descricao_necessidade', this.firstStepRegister.descricao_necessidade)
          formData.set('problemasId', this.firstStepRegister.problemasId)
          formData.set('problema_outro', this.firstStepRegister.problema_outro)
          formData.set('nome_pet', this.pet.name.model)
          formData.set('cor_pelagem', this.pet.color.model)
          formData.set('pedigree', this.pet.pedigree.model)
          formData.set('foto_animal', this.pet.img.model)
          formData.set('nome', this.formUser.name.model)
          formData.set('email', this.formUser.email.model)
          formData.set('cpf', this.formUser.cpf.model)
          formData.set('telefone', this.formUser.phone.model)
          formData.set('nascimento', this.formUser.birth.model)
          formData.set('password', this.formUser.password.model)
          formData.set('cep', this.formUser.cep.model)
          formData.set('numero', this.formUser.number.model)
          formData.set('logradouro', this.formUser.street.model)
          formData.set('bairro', this.formUser.neighborhood.model)
          formData.set('municipio', this.formUser.city.model)
          formData.set('estado', this.formUser.uf.model)
          formData.set('complemento', this.formUser.complement.model)
          globalAxios.post('/cadastrar', formData)
          .then(res => {
            console.log(res)
            this.modalMsg = 'Obrigado por se cadastrar no Iopet! Pedimos que aguarde, pois precisamos analisar seu cadastro. \nEssa análise pode durar até 5 dias e você receberá um e-mail de confirmação de cadastro.'
            this.overlay = true
            this.modal = true
          })
          .catch(error => {
            if(error.response.data.error === 'Email já cadastrado!'){
              this.formUser.email.error.error = true
              this.formUser.email.error.msg = 'E-mail já cadastrado'
            }
            else{
              console.log(error)
            }
          })
        }
        else{
          let formData = new FormData()
          formData.set('data_nascimento', this.firstStepRegister.data_nascimento)
          formData.set('especie_id', this.firstStepRegister.especie_id)
          formData.set('peso', this.firstStepRegister.peso)
          formData.set('porte_id', this.firstStepRegister.porte_id)
          formData.set('problema_saude', this.firstStepRegister.problema_saude)
          formData.set('raca_id', this.firstStepRegister.raca_id)
          formData.set('resgatado', this.firstStepRegister.resgatado)
          formData.set('sexo', this.firstStepRegister.sexo)
          formData.set('vacinacao_emdia', this.firstStepRegister.vacinacao_emdia)
          formData.set('carteira_vacinacao', this.firstStepRegister.carteira_vacinacao)
          formData.set('necessidade_especial', this.firstStepRegister.necessidade_especial)
          formData.set('descricao_necessidade', this.firstStepRegister.descricao_necessidade)
          formData.set('problemasId', this.firstStepRegister.problemasId)
          formData.set('problema_outro', this.firstStepRegister.problema_outro)
          formData.set('nome_pet', this.pet.name.model)
          formData.set('cor_pelagem', this.pet.color.model)
          formData.set('pedigree', this.pet.pedigree.model)
          formData.set('foto_animal', this.pet.img.model)
          axios.post('/cadastrar', formData)
          .then(res => {
            console.log(res)
            this.modalMsg = 'Obrigado por cadastrar seu pet! Pedimos que aguarde, pois precisamos analisar seu cadastro. \nEssa análise pode durar até 5 dias e você receberá um e-mail de confirmação de cadastro.'
            this.overlay = true
            this.modal = true
          })
          .catch(error => {
            console.log(error)
          })
        }
      }
    }
  },
  created () {
    if(EventBus.isAuthenticated()){
      this.$store.dispatch('fetchUser').then(() =>{
        EventBus.$emit('loadingPageCallback', false)
        this.user = JSON.parse(localStorage.getItem('user'))
        this.planValue =  parseInt(this.quotation, 10) + parseInt(this.user.plano.valor_total, 10)
     })
    }
    else{
      EventBus.$emit('loadingPageCallback', false)
    }
  },
  mounted () {
    if(this.$route.params.firstStepRegister && this.$route.params.quotation){
      this.firstStepRegister = this.$route.params.firstStepRegister
      this.quotation = this.$route.params.quotation
    }
    else{
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
  .page{
    min-height: 100vh;
    padding-bottom: 30px;
  }

  .my-container {
    max-width: 950px;
    padding-top: 150px;
  }

  .title{
    color: #FFD26C;
    font-family: ubuntu-medium;
    font-size: 20px;
    margin-bottom: 50px;
  }

  .title span{
    background-color: #E60965;
    color: #E4FDE1;
    padding: 10px 20px;
    margin-left: 20px;
  }

  .sub-title{
    font-size: 15px;
    color: rgba(255,255,255,.7);
  }

  .center{
    max-width: 640px;
    display: block;
    margin: 0 auto;
  }

  .box{
    width: 100%;
    height: auto;
    background-color: #fff;
    padding: 10px 60px 40px 60px;
     -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
  }

  .box label{
    color: #505050;
  }

  .box-title{
    color: #fff;
    font-size: 15px;
    font-family: Lato-bold;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 20px;
  }

  .img-pet{
    position: relative;
  }

  .img-pet .img{
    height: 85px;
    width: 85px;
    border-radius: 50%;
    display: block;
    margin: 10px auto 0 auto;
  }

  #pic{
    display: none;
  }

  .camera{
    position: absolute;
    left: 52%;
    bottom: 5px;
    height: 24px;
    width: auto;
    cursor: pointer;
    transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
  }


  @media only screen and (max-width: 767.98px){
    .title{
      font-size: 17px;
      text-align: center;
      margin-bottom: 20px;
    }

    .sub-title{
      font-size: 13px;
      text-align: center;
    }

    .box-title{
      font-size: 15px;
    }

    label{
      font-size: 12px;
    }

    .box{
      padding: 15px;
    }
  }
</style>



