import Vue from 'vue'
import Vuex from 'vuex'
import axios from './auth'
import EventBus from '@/main'

import {router} from '@/main'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
  },
  mutations: {
  },
  actions: {
    signup ({commit, dispatch}, authData) {
      axios.post('/', {
        email: authData.email,
        password: authData.password
      })
      .then(res => {
        localStorage.setItem('token', res.data.token)
        localStorage.setItem('user', JSON.stringify(res.data.user))
        router.replace('/cliente')
      })
      .catch(error => {
        console.log(error)
      })
    },
    login ({commit, dispatch}, authData, state) {
      axios.post('/login', {
        email: authData.email,
        password: authData.password
      })
      .then(res => {
        Object.assign(axios.defaults, {headers: {token: res.data.token}})
        localStorage.setItem('token', res.data.token)
        localStorage.setItem('user', JSON.stringify(res.data.user))
        router.replace('/cliente')
      })
      .catch(error => {
        EventBus.$emit('loadingPageCallback', false)
        if(error.response.data.error === 'Email não encontrado'){
          EventBus.$emit('emailError', true)
        }
        else if(error.response.data.error === 'Senha inválida.'){
          EventBus.$emit('passwordError', true)
        }
        else{
          console.log(error)
        }
      })
    },
    logout ({commit}) {
      localStorage.removeItem('token')
      localStorage.removeItem('user')
      router.replace('/')
    },
    tryAutoLogin ({commit}) {
      const token = localStorage.getItem('token')
      if (!token) {
        localStorage.removeItem('user')
        return false
      }
      return true
    },
    fetchUser ({commit}) {
      axios.get('/usuario')
        .then(res => {
          localStorage.setItem('user', JSON.stringify(res.data.usuario))
          EventBus.$emit('checkUser')
        })
        .catch(error => {
          console.log(error)
          localStorage.removeItem('token')
          localStorage.removeItem('user')
        })
    }
  },
  getters: {
  }
})



