<template>
  <div :id="elId" class="my-select">
    <div :class="{error: data.error.error}" @click="drop = !drop" class="select">
      <p>{{this.label}}</p>
      <i :class="{active: drop}" class="fas fa-caret-down"></i>
    </div>
    <transition name="fade" mode="out-in">
      <span v-if="data.error.error" class="error-msg">{{data.error.msg}}</span>
    </transition>
    <transition name="fade" mode="out-in">
      <div v-if="drop" class="select-box mshadow-1">
        <transition name="fade" mode="out-in">
        <div v-if="loading" class="loading">
          <div class="center">
            <app-spinner size="30"></app-spinner>
          </div>
        </div>
        <ul v-else class="list-unstyled">
          <li :class="{active: item.active === true}" @click="selected(index)" v-for="(item,index) in options" :key="index">
            {{item.name}}
          </li>
        </ul>
        </transition>
      </div>
    </transition>
  </div>

</template>

<script>
import axios from '@/auth'
import EventBus from '@/main'
import Spinner from '@/components/spinner/Spinner'
export default {
  name: 'HelthInput',
  data () {
    return {
      drop: false,
      label: '',
      options: [],
      loading: true
    }
  },
  components: {
    appSpinner: Spinner
  },
  props: [
    'data',
    'placeholder',
    'elId'
  ],
  methods: {
    selected (index) {
      this.options[index].active = !this.options[index].active
      if(this.options[index].active){
        this.data.model.push(this.options[index].value)
        if(this.options[index].value === 1){
          this.$emit('other', true)
        }
      }
      else{
        const indexOf = this.data.model.indexOf(this.options[index].value)
        if (indexOf > -1) {
          if(this.data.model[indexOf] === 1){
            this.$emit('other', false)
          }
          this.data.model.splice(indexOf, 1)
        }
      }
    }
  },
  mounted () {
    document.addEventListener("click", event => {
      if (!event.target.closest('#'+this.elId))
        this.drop = false
    })
  },
  destroyed () {
    document.removeEventListener("click", event => {})
  },
  created () {
    this.label = this.placeholder
    axios.get('/problemas_saude')
    .then(res => {
      const data = res.data.problema_saude
      const dataProblems = []
      for(let key in data){
        const problem = {
          value: data[key].id,
          name: data[key].nome,
          active: false
        }
        dataProblems.push(problem)
      }
      this.options = dataProblems
      this.loading = false
    })
    .catch(error => {
      console.log(error)
    })
  },
  watch: {
    drop() {
      if(this.drop) {
        this.data.error = false
      }
    }
  }
}
</script>

<style scoped>

</style>



