<template>
  <span>
    <header :style="{top: top+'px'}">
      <div class="log-reg">
        <ul class="list-inline list-unstyled">
          <li @click="login" class="list-inline-item">
            Login
          </li>
          <li class="list-inline-item">
            <router-link to="/cadastro-cotacao">Cadastrar</router-link>
          </li>
        </ul>
      </div>
      <ul class="list-unstyled list-inline desk">
        <li class="list-inline-item">
          <router-link to="/">
            <img class="logo" src="static/imgs/logo1.svg" alt="">
          </router-link>
        </li>
        <li @click="scrollToEl('welcome')" class="list-inline-item">Bem-vindo</li>
        <li @click="scrollToEl('causes')" class="list-inline-item">Causas que Apoiamos</li>
        <li @click="scrollToEl('how-works')" class="list-inline-item">Vantagens do Plano</li>
        <li @click="scrollToEl('price')" class="list-inline-item">Cotação</li>
        <li class="list-inline-item">Blog</li>
      </ul>
      <ul class="list-unstyled list-inline mob">
        <li class="list-inline-item">
          <img @click="menu()" class="menu" src="static/imgs/menu.svg" alt="">
        </li>
        <li class="list-inline-item">
          <router-link to="/">
            <img class="s-logo" src="static/imgs/small-logo.svg" alt="">
          </router-link>
        </li>
      </ul>
    </header>
  </span>
</template>

<script>
import EventBus from '@/main'
export default {
  name: 'IndexHeader',
  data () {
    return {
      top: 30.0
    }
  },
  methods: {
    menu () {
      this.$emit('menu')
    },
    scrollToEl (e) {
      document.getElementById(e).scrollIntoView({ block: 'start',  behavior: 'smooth' })
    },
    login() {
      if(EventBus.isAuthenticated()){
        this.$router.push({name: 'ClientPage'})
      }
      else{
        this.$router.push({name: 'LoginPage'})
      }
    },
    handleScroll () {
      if(window.scrollY > 120){
        while(this.top > 0){
          this.top--
        }
      }
      else{
        while(this.top < 30){
          this.top++
        }
      }
    }
  },
  created () {
    window.addEventListener('scroll', this.handleScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style scoped>
  header{
    height: 80px;
    width: calc(100% - 400px);
    line-height: 80px;
    background-color: #fff;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    position: fixed;
    left: 0;
    padding-left: 80px;
    padding-right: 80px;
    color: #505050;
    z-index: 500;
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    -ms-transition: all .3s;
  }

  .logo{
    height: 48px;
    width: auto;
  }

  ul li{
    padding-right: 15px;
    padding-left: 15px;
    cursor: pointer;
    transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -webkit-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
  }

  ul li:hover{
    color: #E60965;
  }

  ul li:first-child{
    padding-left: 0;
  }

  ul li:last-child{
    padding-right: 0;
  }

  .log-reg{
    width: 250px;
    height: 40px;
    line-height: 40px;
    background-color: #E60965;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(50%, -50%);
    -moz-transform: translate(50%, -50%);
    -webkit-transform: translate(50%, -50%);
    -ms-transform: translate(50%, -50%);
    -o-transform: translate(50%, -50%);
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    text-align: center;
  }

  .log-reg ul li:first-child{
    color: #fff;
  }

  .log-reg ul li:last-child{
    color: #FFD26C;
  }

  @media only screen and (max-width: 1440px){
    header{
      width: 1120px;
    }
  }

  @media only screen and (max-width: 1250px){
    header{
      width: 950px;
    }

    ul li{
      padding-right: 5px;
      padding-left: 5px;
    }

    .log-reg{
      width: 200px;
    }
  }

  @media only screen and (max-width: 1100px){
    header{
      width: 850px;
    }
  }

  @media only screen and (max-width: 992px){
    header{
      width: 160px;
      padding-left: 28px;
      padding-right: 15px;
    }

    .log-reg{
      right: -80px;
    }

    .s-logo{
      width: 47px;
      height: auto;
    }

    .menu{
      width: 25px;
      height: auto;
    }
  }
</style>



