<template>
  <div class="doubts">
    <h2>Tem uma dúvida?</h2>
    <div v-for="(item, index) in questions" :key="index" class="item">
      <div class="row">
        <div class="col-auto">
          <img @click="toggleActive(index)" :class="{active: getActive(index)}" class="arrow" src="static/imgs/arrow_down.svg" alt="">
        </div>
        <div class="col">
          <p class="question">{{item.titulo}}</p>
          <transition name="fade" mode="out-in">
            <p v-if="getActive(index)" class="answer">{{item.texto}}</p>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '@/auth'
import EventBus from '@/main'
export default {
  name: 'Doubts',
  data () {
    return {
      questions: []
    }
  },
  methods: {
    toggleActive (index) {
      this.questions[index].active = !this.questions[index].active
      return this.questions[index].active
    },
    getActive(index) {
      return this.questions[index].active
    }
  },
  created () {
    EventBus.$on('loadQuestions', () => {
      axios.get('/duvidas')
      .then(res => {
        const data = res.data.duvidas
        const dataQuestions = []
        for(let key in data){
          const question = data[key]
          question.active = false
          dataQuestions.push(question)
        }
        this.questions = dataQuestions
        EventBus.$emit('loadingPageCallback', false)
      })
      .catch(error => {
        console.log(error)
      })
    })
  }
}
</script>

<style scoped>
  .doubts{
    width: 100%;
    max-width: 385px;
    height: 570px;
    overflow-y: auto;
    background-color: #1E62AC;
    color: #fff;
    padding: 30px;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
  }

  .doubts::-webkit-scrollbar {
    width: 5px;
    height: 20px !important;
  }

  /* Track */
  .doubts::-webkit-scrollbar-track {
    background: transparent;
  }

  /* Handle */
  .doubts::-webkit-scrollbar-thumb {
    background: rgba(255,255,255,.5);
    border-radius: 3px;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                      0 1px 5px 0 rgba(0, 0, 0, 0.12),
                      0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
              0 1px 5px 0 rgba(0, 0, 0, 0.12),
              0 3px 1px -2px rgba(0, 0, 0, 0.2);
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
    -ms-transition: all .3s;
    -o-transition: all .3s;
    height: 20px !important;
  }

  .doubts::-webkit-scrollbar-track-piece{
    height: 20px !important;
  }

  /* Handle on hover */
  .doubts::-webkit-scrollbar-thumb:hover {
    border-color: #555;
  }

  h2{
    font-family: ubuntu-medium;
    font-size: 31px;
    margin-bottom: 80px;
    padding-left: 25px;
  }

  .arrow{
    height: 9px;
    width: auto;
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
    -ms-transition: all .3s;
    -o-transition: all .3s;
    cursor: pointer;
  }

  .arrow.active{
    transform: rotateX(180deg);
    -moz-transform: rotateX(180deg);
    -webkit-transform: rotateX(180deg);
    -ms-transform: rotateX(180deg);
    -o-transform: rotateX(180deg);
  }

  .question{
    font-family: Lato-bold;
    margin-left: -10px;
  }

  .answer{
    margin-left: -10px;
    font-family: Lato-light;
    color: rgba(255,255,255,.85);
  }

  @media only screen and (max-width: 992px){
    h2{
      font-size: 28px;
      margin-bottom: 45px;
    }
  }

  @media only screen and (max-width: 767.98px){
    .doubts{
      display: block;
      margin-right: auto;
      margin-left: auto;
      margin-bottom: 30px;
    }
  }
</style>



