<template>
  <div class="input-content">
    <input
      :id="elId"
      @focus="data.error.error = false"
      :class="[className, { error: data.error.error}]"
      type="text"
      :placeholder="placeholder"
      ref="textInput"
      :value="data.model"
      @input="updated"
    >
    <transition name="fade" mode="out-in">
      <span v-if="data.error.error" class="error-msg">{{data.error.msg}}</span>
    </transition>
  </div>
</template>

<script>
import IMask from 'imask'
export default {
  name: 'TextInput',
  data () {
    return {

    }
  },
  props: [
    'data',
    'placeholder',
    'className',
    'elId',
    'mask'
  ],
  methods: {
    updated () {
      if(this.mask){
        const id = this.elId
        let element = document.getElementById(id)
        //CPF
        if(this.mask === 'cpf'){
          let mask = new IMask(element, {
            mask: '000.000.000-00'
          })
          this.$emit('input',
            this.data.model = mask.value
          )
        }
        //cardDate
        else if(this.mask === 'cardDate'){
          let mask = new IMask(element, {
            mask: '00/0000'
          })
          this.$emit('input',
            this.data.model = mask.value
          )
        }
        //cardCode
        else if(this.mask === 'cardCode'){
          let mask = new IMask(element, {
            mask: '0000'
          })
          this.$emit('input',
            this.data.model = mask.value
          )
        }
        //cardNumber
        else if(this.mask === 'cardNumber'){
          let mask = new IMask(element, {
            mask: '0000 0000 0000 0000'
          })
          this.$emit('input',
            this.data.model = mask.value
          )
        }
        //addressNumber
        else if(this.mask === 'addressNumber'){
          let mask = new IMask(element, {
            mask: '00000'
          })
          this.$emit('input',
            this.data.model = mask.value
          )
        }
        //cep
        else if(this.mask === 'cep'){
          let mask = new IMask(element, {
            mask: '00.000-000'
          })
          this.$emit('input',
            this.data.model = mask.value
          )
        }
        //weight
        else if(this.mask === 'decimals'){
          let value = this.$refs.textInput.value
          let integer = value.split(',')[0]
          value = value.replace(/\D/, '')
          value = value.replace(/^[0]+/, '')

          if(value.length <= 3 || !integer){
            if(value.length === 1)
              value = '0,00' + value
            if(value.length === 2)
              value = '0,0' + value
            if(value.length === 3)
              value = '0,' + value
          } else {
            value = value.replace(/^(\d{1,})(\d{3})$/, '$1,$2')
          }
          this.$emit('input',
            this.data.model = value
          )
        }
        //date
        else if(this.mask === 'date'){
          let mask = new IMask(element, {
            mask: Date,
            pattern: 'd/`m/`Y',
            groups: {
              d: new IMask.MaskedPattern.Group.Range([1, 31]),
              m: new IMask.MaskedPattern.Group.Range([1, 12]),
              Y: new IMask.MaskedPattern.Group.Range([1900, 99999])
            }
          })
          this.$emit('input',
            this.data.model = mask.value
          )
        }
      }
      else{
        this.$emit('input',
          this.data.model = this.$refs.textInput.value
        )
      }
    }
  }
}
</script>

<style scoped>
  .input-content{
    position: relative;
  }
</style>



