<template>
  <header :style="{top: top+'px'}">
    <transition name="fade" mode="out-in">
      <div v-if="userMenu" class="user-menu">
        <ul class="list-unstyled">
          <li>
            <router-link to="/meu-perfil">Ver meu perfil</router-link>
          </li>
          <li @click="logout">
            Sair
          </li>
        </ul>
      </div>
    </transition>
    <div class="container-fluid">
      <div class="row">
        <div class="col">
          <img @click="back" src="static/imgs/back.svg" alt="" class="back">
        </div>
        <div class="col text-center">
          <router-link to="/cliente">
            <img src="static/imgs/logo1.svg" alt="" class="logo">
          </router-link>
        </div>
        <div v-if="auth" class="col text-right">
          <div class="user-content">
            <p @click="userMenu = !userMenu" class="name">Olá, {{userName}}</p>
            <img @click="userMenu = !userMenu" class="profile" :src="img ? img : 'static/imgs/default_pet.svg'" alt="">
          </div>
        </div>
        <div v-else class="col text-right">
          <router-link to="/login" class="login">Login</router-link>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import EventBus from '@/main'
export default {
  name: 'QuotationHeader',
  data () {
    return {
      userMenu: false,
      top: 30,
      userName: '',
      img: ''
    }
  },
  computed: {
    auth() {
      return EventBus.isAuthenticated()
    }
  },
  methods: {
    handleScroll () {
      if(window.scrollY > 120){
        while(this.top > 0){
          this.top--
        }
      }
      else{
        while(this.top < 30){
          this.top++
        }
      }
    }
  },
  created () {
    window.addEventListener('scroll', this.handleScroll)

    if (EventBus.isAuthenticated()) {
      //cutting username
      const user = JSON.parse(localStorage.getItem('user'))
      const [first, ...rest] = user.nome.split(" ")
      this.userName = first
      this.img = user.foto
    }
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style scoped>

  header{
    background-color: #fff;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    position: fixed;
    left: 0;
    padding-left: 90px;
    padding-right: 130px;
    color: #505050;
    z-index: 500;
    height: 80px;
    line-height: 80px;
    width: 100%;
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    -ms-transition: all .3s;
  }

  .logo{
    height: 48px;
    width: auto;
  }

  .back{
    height: 30px;
    width: auto;
    cursor: pointer;
  }

  .login{
    color: #E60965;
    font-family: lato-bold;
  }


  .profile{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    display: inline-block;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    position: relative;
    z-index: 200;
    margin-right: 45px;
  }

  .name{
    display: inline-block;
    color:#1E62AC;
    font-size: 16px;
    font-family: lato-bold;
    margin-right: 10px;
    cursor: pointer;
    position: relative;
    z-index: 200;
  }

  .user-menu{
    position: fixed;
    width: 220px;
    height: 160px;
    background-color: #fff;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    right: 175px;
    top: 20px;
    z-index: 199;
    text-align: right;
  }

  .user-menu ul{
    line-height: 40px;
  }

  .user-menu ul li:first-child{
    margin-top: 80px;
    color:#1E62AC;
  }

  .user-menu ul li{
    font-size: 16px;
    font-family: lato-bold;
    cursor: pointer;
    margin-right: 90px;
    color: #FF2424;
  }

  @media only screen and (max-width: 992px){
    header{
      padding-right: 15px;
      padding-left: 15px;
    }

    .name,
    .user-menu ul li{
      font-size: 13px;
    }

    .user-menu ul li{
      margin-right: 85px
    }

    .user-menu ul li:first-child{
      margin-top: 45px;
    }

    .user-menu{
      height: 135px;
      right: 42px;
      top: 40px;
    }

    .profile{
      height: 38px;
      width: 38px;
    }

    .logo{
      height: 42px;
    }
  }

  @media only screen and (max-width: 767.98px){
    header{
      padding-right: 0;
      padding-left: 0;
    }
    .name{
      display: none;
    }
    .profile{
      margin-right: 0;
    }
    .back{
      height: 24px;
    }
    .logo{
      height: 30px;
    }
    .user-menu{
      right: 10px;
    }
  }
</style>



