<template>
  <div class="page">

    <div v-if="success" class="overlay"></div>
    <transition name="fade" mode="out-in">
      <div v-if="success" class="success-modal text-center">
        <p class="title">Sucesso!</p>
        <p class="desc">Foi enviado para o seu e-mail um link e instruções para a <br> recuperação de sua senha.</p>
        <button @click="success = false" class="btn btn-black">ok</button>
      </div>
    </transition>

    <router-link to="/">
      <img src="static/imgs/logo1.svg" alt="" class="logo">
    </router-link>

    <router-link to="/login">
      <img class="closes" src="static/imgs/close.svg" alt="">
    </router-link>

    <div class="box text-center">
      <img src="static/imgs/dog.svg" alt="" class="dog">
      <p class="forgot">Esqueceu sua senha?</p>
      <div class="input-box">
        <button @click="send" class="btn btn-send disabled">ok</button>
        <input v-model="email" type="email" class="wbg" placeholder="E-mail">
      </div>
      <p class="problem">Trancado do lado de fora? Entre em contato no <span>(00) 99999-9999</span> <br> que tentaremos resolver o problema.</p>
    </div>

  </div>
</template>

<script>
import EventBus from '@/main'
export default {
  name: 'ForgotPasswordPage',
  data () {
    return {
      email: '',
      success: false
    }
  },
  watch: {
    email () {
      let btn = document.getElementsByClassName('btn')[0]
      if(this.email.length>0){
        btn.classList.remove('disabled')
      }
      else{
        btn.classList.add('disabled')
      }
    }
  },
  methods: {
    send () {
      this.success = true
    }
  },
  created () {
    EventBus.$emit('loadingPageCallback', false)
  }
}
</script>

<style scoped>

  .page{
    height: 100vh;
    position: relative;
  }

  .logo{
    height: 50px;
    width: auto;
    position: fixed;
    top: 50px;
    left: 50%;
    transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
  }

  .closes{
    position: fixed;
    top: 50px;
    right: 50px;
    height: 25px;
    width: auto;
  }

  .box{
    position: fixed;
    top: 55%;
    left: 50%;
    transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
  }

  .dog{
    height: 145px;
    width: auto;
  }

  .forgot{
    font-size: 28px;
    color: #676767;
    font-family: ubuntu-regular;
    margin-top: 15px;
    margin-bottom: 20px;
  }

  .input-box{
    position: relative;
  }

  .input-box input{
    width: 320px !important;
    padding-right: 100px !important;
  }

  .input-box .btn{
    position: absolute;
    top: 5px;
    right: 45px;
  }

  .problem{
    margin-top: 60px;
    color: #676767;
    font-family: lato-regular;
  }

  .problem span{
    font-family: lato-bold;
    color: #E60965;
  }

  .overlay{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 899;
  }

  .success-modal{
    width: 460px;
    height: 210px;
    background-color: #fff;
    position: fixed;
    top: 55%;
    left: 50%;
    transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
    z-index: 900;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
  }

  .success-modal .btn{
    width: 120px;
    height: 30px;
  }

  .success-modal .title{
    font-size: 18px;
    color: #E60965;
    font-family: ubuntu-medium;
    margin-top: 20px;
  }

  .success-modal .desc{
    color: #505050;
    font-family: ubuntu-regular;
    margin-bottom: 50px;
  }

  @media only screen and (max-width: 767.98px){
    .closes{
      top: 15px;
      right: 15px;
    }

    .box{
      position: static;
      display: block;
      margin: 25vh auto 0 auto;
      transform: none;
    }

    .page{
      min-height: auto;
    }

    .input-box{
      max-width: 300px !important;
      display: block;
      margin: 0 auto;
    }

    .input-box input{
      width: 100% !important;
    }

    .input-box .btn{
      right: 10px;
    }

    .success-modal{
      max-width: 300px;
      height: auto;
      padding-bottom: 15px;
    }
  }
</style>



