<template>
  <div class="loading-page">
    <app-spinner class="center-center"></app-spinner>
  </div>
</template>

<script>
import Spinner from '@/components/spinner/Spinner'
export default {
  name: '',
  data () {
    return {

    }
  },
  components: {
    appSpinner: Spinner
  }
}
</script>

<style scoped>
  .loading-page{
    width: 100%;
    height: 100%;
    background-color: rgba(255,255,255,.7);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9999999;
  }

  .center-center{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
  }
</style>



