<template>
  <header :class="{active: active}">
    <transition name="fade" mode="out-in">
      <div v-if="userMenu" class="user-menu">
        <ul class="list-unstyled">
          <li>
            <router-link to="/meu-perfil">Ver meu perfil</router-link>
          </li>
          <li @click="logout">
            Sair
          </li>
        </ul>
      </div>
    </transition>
    <div class="container-fluid">
      <div class="row">
        <div class="col">
          <router-link to="/cliente">
            <img src="static/imgs/logo1.svg" alt="" class="logo desk">
          </router-link>
        </div>
        <div class="col text-center">
          <router-link to="/cliente">
            <img src="static/imgs/logo1.svg" alt="" class="logo mob">
          </router-link>
          <ul class="list-unstyled list-inline opts desk">
            <li :class="{active: pets}" @click="myPet()" class="list-inline-item">
                Meu pet
            </li>
            <li class="list-inline-item">
              <router-link to="/financeiro" active-class="active">
                Financeiro
              </router-link>
             </li>
            <li class="list-inline-item">
              <router-link to="/duvidas" active-class="active">
                Dúvidas
              </router-link>
            </li>
          </ul>
        </div>
        <div class="col text-right">
          <div class="user-content">
            <p @click="userMenu = !userMenu" class="name">Olá, {{userName}}</p>
            <img @click="userMenu = !userMenu" class="profile" :src="img ? img : 'static/imgs/default_pet.svg'" alt="">
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col text-center">
          <ul class="list-unstyled list-inline opts mob">
            <li :class="{active: pets}" @click="myPet()" class="list-inline-item">
                Meu pet
            </li>
            <li class="list-inline-item">
              <router-link to="/financeiro" active-class="active">
                Financeiro
              </router-link>
             </li>
            <li class="list-inline-item">
              <router-link to="/duvidas" active-class="active">
                Dúvidas
              </router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import EventBus from '@/main'
export default {
  name: 'ClientHeader',
  data () {
    return {
      userMenu: false,
      active: false,
      userName: '',
      img: ''
    }
  },
  props: {
    pets: {
      default: false,
      type: Boolean
    }
  },
  methods: {
    handleScroll () {
      if(window.scrollY >= 80){
        this.active = true
      }
      else{
        this.active = false
      }
    },
    myPet () {
      if(this.$route.name != 'PetsPage'){
        this.$router.push({name: 'PetsPage', params: {index: 0}})
      }
    }
  },
  created () {
    window.addEventListener('scroll', this.handleScroll)

    if (EventBus.isAuthenticated()) {
      //cutting username
      const user = JSON.parse(localStorage.getItem('user'))
      const [first, ...rest] = user.nome.split(" ")
      this.userName = first
      this.img = user.foto
    }

    EventBus.$on('checkUser', () => {
      const user = JSON.parse(localStorage.getItem('user'))
      const [first, ...rest] = user.nome.split(" ")
      this.userName = first
      this.img = user.foto
    })
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style scoped>

  header{
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff;
    z-index: 500;
    color: #505050;
    width: 100%;
    font-size: 16px;
    font-family: lato-bold;
    height: 120px;
    line-height: 120px;
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
    -ms-transition: all .3s;
    -o-transition: all .3s;
  }

  header.active{
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    line-height: 80px;
    height: 80px;
  }

  .opts li{
    margin-right: 15px;
    margin-left: 15px;
    cursor: pointer;
    transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -webkit-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
  }

  .opts li:hover,
  .opts li a.active{
    color: #E60965;
  }

  .opts li.active{
    color: #E60965;
  }

  .opts li:first-child{
    margin-left: 0;
  }

  .opts li:last-child{
    margin-right: 0;
  }

  .logo{
    height: 48px;
    width: auto;
    margin-left: 90px;
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
    -ms-transition: all .3s;
    -o-transition: all .3s;
  }

  .profile{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    display: inline-block;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    position: relative;
    z-index: 200;
    margin-right: 45px;
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
    -ms-transition: all .3s;
    -o-transition: all .3s;
  }

  .name{
    display: inline-block;
    color:#1E62AC;
    font-size: 16px;
    font-family: lato-bold;
    margin-right: 10px;
    cursor: pointer;
    position: relative;
    z-index: 200;
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
    -ms-transition: all .3s;
    -o-transition: all .3s;
  }

  .user-menu{
    position: fixed;
    width: 220px;
    height: 175px;
    background-color: #fff;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    right: 30px;
    top: 20px;
    z-index: 199;
    text-align: right;
  }

  .user-menu ul{
    line-height: 40px;
  }

  .user-menu ul li:first-child{
    margin-top: 80px;
    color:#1E62AC;
  }

  .user-menu ul li{
    font-size: 16px;
    font-family: lato-bold;
    cursor: pointer;
    margin-right: 90px;
    color: #FF2424;
  }

  @media only screen and (max-width: 992px){
    header{
      padding-top: 30px;
      line-height: 30px;
    }

     header.active{
      padding-top: 30px;
      line-height: 30px;
      line-height: 30px;
      height: initial;
    }

    .logo.mob{
      margin-left: 0;
      display: inline-block;
      height: 42px;
      margin-bottom: 15px;
    }

    .name,
    .user-menu ul li{
      font-size: 13px;
    }

    .user-menu ul li{
      margin-right: 85px
    }

    .user-menu ul li:first-child{
      margin-top: 45px;
    }

    .user-menu{
      height: 135px;
    }

    .profile{
      height: 38px;
      width: 38px;
    }
  }

  @media only screen and (max-width: 767.98px){
    header{
      padding-right: 0;
      padding-left: 0;
    }

    .name{
      display: none;
    }

    .profile{
      margin-right: 0;
    }

    .user-menu{
      right: 10px;
    }

    .logo.mob{
      height: 30px;
    }

    .opts.mob{
      font-size: 14px;
    }
  }
</style>



