<template>
  <div class="input-content">
    <textarea
      cols="30"
      rows="5"
      @focus="data.error.error = false"
      :class="[className, { error: data.error.error}]"
      :placeholder="placeholder"
      ref="textareaInput"
      :value="data.model"
      @input="updated"
    >
    </textarea>
    <transition name="fade" mode="out-in">
      <span v-if="data.error.error" class="error-msg">{{data.error.msg}}</span>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'TextareaInput',
  data () {
    return {
    }
  },
  props: [
    'data',
    'placeholder',
    'className'
  ],
  methods: {
    updated () {
      this.$emit('input',
        this.data.model = this.$refs.textareaInput.value
      )
    }
  }
}
</script>

<style scoped>
  .input-content{
    position: relative;
  }
</style>



