import {store} from './store'

export const routes = [
  {
    path: '/',
    name: 'IndexPage',
    component: require('@/components/pages/IndexPage').default
  },
  {
    path: '/login',
    name: 'LoginPage',
    component: require('@/components/pages/LoginPage').default
  },
  {
    path: '/esqueci-minha-senha',
    name: 'ForgotPasswordPage',
    component: require('@/components/pages/ForgotPasswordPage').default
  },
  {
    path: '/cadastro-cotacao',
    name: 'QuotationPage',
    component: require('@/components/pages/quotation/QuotationPage').default
  },
  {
    path: '/cadastro-cotacao-plano',
    name: 'PlanQuotationPage',
    component: require('@/components/pages/quotation/PlanQuotationPage').default
  },
  {
    path: '/cliente',
    name: 'ClientPage',
    component: require('@/components/pages/client/ClientPage').default,
    beforeEnter (to, from, next) {
      if (localStorage.getItem('token')) {
        next()
      } else {
        next('/login')
      }
    }
  },
  {
    path: '/meu-pet/:index',
    name: 'PetsPage',
    component: require('@/components/pages/pets/PetsPage').default,
    beforeEnter (to, from, next) {
      if (localStorage.getItem('token')) {
        next()
      } else {
        next('/login')
      }
    }
  },
  {
    path: '/remover-pet/:id',
    name: 'PetCancellationPage',
    component: require('@/components/pages/cancellations/PetCancellationPage').default,
    beforeEnter (to, from, next) {
      if (localStorage.getItem('token')) {
        next()
      } else {
        next('/login')
      }
    }
  },
  {
    path: '/financeiro',
    name: 'FinancialPage',
    component: require('@/components/pages/financial/FinancialPage').default,
    beforeEnter (to, from, next) {
      if (localStorage.getItem('token')) {
        next()
      } else {
        next('/login')
      }
    }
  },
  {
    path: '/duvidas',
    name: 'DoubtsPage',
    component: require('@/components/pages/doubts/DoubtsPage').default,
    beforeEnter (to, from, next) {
      if (localStorage.getItem('token')) {
        next()
      } else {
        next('/login')
      }
    }
  },
  {
    path: '/meu-perfil',
    name: 'ProfilePage',
    component: require('@/components/pages/client/ProfilePage').default,
    beforeEnter (to, from, next) {
      if (localStorage.getItem('token')) {
        next()
      } else {
        next('/login')
      }
    }
  },
  {
    path: '/cancelar-plano',
    name: 'PlanCancellationPage',
    component: require('@/components/pages/cancellations/PlanCancellationPage').default,
    beforeEnter (to, from, next) {
      if (localStorage.getItem('token')) {
        next()
      } else {
        next('/login')
      }
    }
  },
]



