<template>
  <div class="my-pet text-center">
    <p class="name">{{pet.name}}</p>
    <p class="id">ID {{pet.id}}</p>
    <form enctype="multipart/form-data">
      <input class="hid" @change="handleFileUpload" :id="'pet-pic'+pet.id" type="file">
        <div class="img-content">
          <img class="pic" :src="(pet.img) ? pet.img : 'static/imgs/default_pet.svg'" alt="">
          <label :for="'pet-pic'+pet.id">
            <img class="edit-pic" src="static/imgs/camera.svg" alt="">
          </label>
        </div>
    </form>
    <p class="breed">{{pet.breed}}</p>
    <p class="age">{{pet.age}} anos</p>
    <router-link :to="{name: 'PetsPage', params: {index: index}}">
      <p class="details">Ver detalhes</p>
    </router-link>
  </div>
</template>

<script>
import axios from '@/auth'
import EventBus from '@/main'
export default {
  name: 'Pet',
  data () {
    return {
    }
  },
  props: [
    'pet',
    'index'
  ],
   methods: {
    handleFileUpload(e) {
      let formData = new FormData()
      formData.append('foto_animal', e.target.files[0])
      formData.append('animal_id', this.pet.id)
      axios.post('/imagem/pet', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
      .then(res => {
        let reader = new FileReader()
        reader.onloadend = ( () => {
          this.pet.img = reader.result
        })
        if(e.target.files[0]){
          reader.readAsDataURL(e.target.files[0])
        }
      })
      .catch(error => {
        console.log(error)
      })
    }
  }
}
</script>

<style scoped>
  .my-pet{
    width: 100%;
    height: auto;
    margin-bottom: 30px;
  }

  .name{
    font-family: ubuntu-bold;
    color: #1E62AC;
    font-size: 24px;
    margin-bottom: 0;
  }

  .id{
    color: #707070;
    font-family: Lato-regular;
    font-size: 11px;
  }

  .hid{
    display: none;
  }

  .img-content{
    position: relative;
  }

  .pic{
    height: 145px;
    width: 145px;
    border-radius: 50%;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
  }

  .breed{
    color: #505050;
    font-size: 18px;
    margin-top: 10px;
    margin-bottom: 0;
  }

  .age{
    color: #505050;
    font-size: 18px;
    margin-bottom: 5px;
  }

  .details{
    color: #E60965;
    font-family: Lato-bold;
    cursor: pointer;
  }

  .edit-pic{
    position: absolute;
    height: 32px;
    width: auto;
    bottom: 0;
    left: 60%;
    cursor: pointer;
  }

  @media only screen and (max-width: 992px){
    .name{
      font-size: 18px;
    }

    .id{
      font-size: 7px;
    }

    .pic{
      height: 110px;
      width: 110px;
    }

    .edit-pic{
      height: 24px;
      width: 24px;
    }

    .breed,
    .age{
      font-size: 12px;
    }

    .details{
      font-size: 10px;
    }

    .my-pet{
      margin-bottom: 20px;
    }
  }
</style>



