<template>
  <div :id="elId" class="my-select">
    <div :class="{error: data.error.error}" @click="drop = !drop" class="select">
      <p>{{this.label}}</p>
      <i :class="{active: drop}" class="fas fa-caret-down"></i>
      <input type="number" ref="selectInput" :value="data.model"
      @input="updated">
    </div>
    <transition name="fade" mode="out-in">
      <span v-if="data.error.error" class="error-msg">{{data.error.msg}}</span>
    </transition>
    <transition name="fade" mode="out-in">
      <div v-if="drop" class="select-box mshadow-1">
        <transition name="fade" mode="out-in">
        <div v-if="loading" class="loading">
          <div class="center">
            <app-spinner size="30"></app-spinner>
          </div>
        </div>
        <ul v-else class="list-unstyled">
          <li @click="selected(index)" v-for="(item,index) in options" :key="index">
            {{item.name}}
          </li>
        </ul>
        </transition>
      </div>
    </transition>
  </div>

</template>

<script>
import axios from '@/auth'
import EventBus from '@/main'
import Spinner from '@/components/spinner/Spinner'
export default {
  name: 'BreedInput',
  data () {
    return {
      drop: false,
      label: '',
      options: [],
      loading: true,
      specie: null
    }
  },
  props: [
    'data',
    'placeholder',
    'elId'
  ],
  components: {
    appSpinner: Spinner
  },
  methods: {
    updated (value) {
      this.$emit('input',
        this.data.model = value
      )
    },
    selected (index) {
      this.$refs.selectInput.value = this.options[index].value
      const value = this.$refs.selectInput.value
      this.updated(value)
      this.label = this.options[index].name
      this.drop = false
      EventBus.$emit('loadShape', {
        specie: this.specie,
        breed: this.options[index].value
      })
    }
  },
  created () {
    this.label = this.placeholder
    EventBus.$on('loadBreed', specie => {
      this.specie = specie
       axios.get('/racas/'+specie)
      .then(res => {
        const data = res.data.racas
        const dataBreeds = []
        for(let key in data){
          const breed = {
            value: data[key].id,
            name: data[key].nome
          }
          dataBreeds.push(breed)
        }
        this.options = dataBreeds
        this.loading = false
      })
      .catch(error => {
        console.log(error)
      })
    })

    EventBus.$on('clean', () => {
      this.drop = false
      this.loading = true
      this.data.model = null
      this.label = this.placeholder
    })
  },
  mounted () {
    document.addEventListener('click', event => {
      if (!event.target.closest('#'+this.elId))
        this.drop = false
    })
  },
  destroyed () {
    document.removeEventListener('click', event => {})
  },
  watch: {
    drop() {
      if(this.drop) {
        this.data.error = false
      }
    }
  }
}
</script>

<style scoped>

</style>



