<script>
import axios from '@/auth'
import EventBus from '@/main'
import Footer from '@/components/footer/Footer'
import ClientHeader from '@/components/header/ClientHeader'
export default {
  name: 'PetsPage',
  data () {
    return {
      doctor: '',
      showPets: false,
      months: false,
      filter: false,
      year: 2018,
      monthsList: [
        {label: 'Jan', value: 1, active: false},
        {label: 'Fev', value: 2, active: false},
        {label: 'Mar', value: 3, active: false},
        {label: 'Abr', value: 4, active: false},
        {label: 'Mai', value: 5, active: false},
        {label: 'Jun', value: 6, active: false},
        {label: 'Jul', value: 7, active: false},
        {label: 'Ago', value: 8, active: false},
        {label: 'Set', value: 9, active: false},
        {label: 'Out', value: 10, active: false},
        {label: 'Nov', value: 11, active: false},
        {label: 'Dez', value: 12, active: false}
      ],
      user: {},
      pets: [],
      activePet: {},
      historicList: [],
      index: null,
      flag1: true,
      flag2: true,
      searchFlag: false
    }
  },
  components: {
    appClientHeader: ClientHeader,
    appFooter: Footer
  },
  methods: {
    getMonths(){
      let months = []
      for(let key in this.monthsList){
        if(this.monthsList[key].active){
          months.push(this.monthsList[key].label)
        }
      }
      return months
    },
    search(){
      EventBus.$emit('loadingPageCallback', true)
      const mes = []
      for(let key in this.monthsList){
        if(this.monthsList[key].active){
          mes.push(this.monthsList[key].value)
        }
      }
      const formData = {
        animal_id: this.activePet.id,
        nome_medico: this.doctor,
        ano: this.year,
        mes: mes
      }
      axios.post('/filtrar/atendimento',formData)
      .then(res => {
        const data = res.data.filtro
        const historicList = []
        for(let key in data){
          const historic = {
            id: data[key].id,
            animal: data[key].animal_id,
            date: data[key].data_atendimento,
            doctor: data[key].medico.nome,
            value: data[key].valor,
            crvm: data[key].medico.crvm,
            prodecure: data[key].procedimento
          }
          historicList.push(historic)
        }
        this.historicList = historicList
        EventBus.$emit('loadingPageCallback', false)
        this.filter = false
        this.searchFlag = true
      })
      .catch(error => {
        console.log(error)
        EventBus.$emit('loadingPageCallback', false)
      })
    },
    incYear(){
      const date = new Date()
      const year = date.getFullYear()
      if(this.year < year){
        this.year++
      }
    },
    decYear(){
      this.year--
      if(this.year>2018){

      }
    },
    toggleMonth(index){
      this.monthsList[index].active = !this.monthsList[index].active
    },
    removePet(){
      this.$router.push({name: 'PetCancellationPage', params: {id: this.activePet.id}})
    },
    handleFileUpload(e) {
      let formData = new FormData()
      formData.append('foto_animal', e.target.files[0])
      formData.append('animal_id', this.activePet.id)
      axios.post('/imagem/pet', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
      .then(res => {
        let reader = new FileReader()
        reader.onloadend = ( () => {
          this.activePet.img = reader.result
        })
        if(e.target.files[0]){
          reader.readAsDataURL(e.target.files[0])
        }
      })
      .catch(error => {
        console.log(error)
      })
    },
    setPet (index) {
      this.activePet = this.pets[index]
      this.showPets = false
      this.getHistoric (this.activePet.id, '')
    },
    loadHistoric () {
      if(this.historicList.length > 0) {
        const lastId = this.historicList[this.historicList.length-1].id
        const petId = this.activePet.id
        axios.get('/historico/?animal_id='+petId+'&registro_id='+lastId)
        .then(res => {
          const data = res.data.historico
          for(let key in data){
            const historic = {
              id: data[key].id,
              animal: data[key].animal_id,
              date: data[key].data_atendimento,
              doctor: data[key].medico.nome,
              value: data[key].valor,
              crvm: data[key].medico.crvm,
              prodecure: data[key].procedimento
            }
            this.historicList.push(historic)
          }
          this.flag1 = true
          this.flag2 = true
        })
        .catch(error => {
          console.log(error)
        })
      }
      else{
        this.flag1 = true
        this.flag2 = true
      }
    },
    getHistoric (petId, last) {
      EventBus.$emit('loadingPageCallback', true)
      axios.get('/historico/?animal_id='+petId+'&registro_id='+last)
      .then(res => {
        const data = res.data.historico
        const historicList = []
        for(let key in data){
          const historic = {
            id: data[key].id,
            animal: data[key].animal_id,
            date: data[key].data_atendimento,
            doctor: data[key].medico.nome,
            value: data[key].valor,
            crvm: data[key].medico.crvm,
            prodecure: data[key].procedimento
          }
          historicList.push(historic)
        }
        this.historicList = historicList
        EventBus.$emit('loadingPageCallback', false)
      })
      .catch(error => {
        console.log(error)
      })
    }
  },
  mounted () {
    this.index = this.$route.params.index
    this.$store.dispatch('fetchUser').then(() =>{
      this.user = JSON.parse(localStorage.getItem('user'))
      axios.get('/pets')
      .then(res => {
        const data = res.data.pets
        const pets = []
        for(let key in data){
          const pet = {
            name: data[key].nome,
            img: data[key].imagem,
            id: data[key].id,
            age: data[key].idade,
            breed: data[key].raca_nome.nome,
            sex: data[key].sexo,
            rescued: data[key].resgatado,
            specie: data[key].especie_id,
            color: data[key].cor_pelagem,
            pedigree: data[key].pedigree,
            specialNeeds: data[key].necessidade_especial
          }
          pets.push(pet)
        }
        this.pets = pets
        this.activePet = this.pets[this.index]
        this.getHistoric(this.activePet.id, '')
      })
      .catch(error => {
        console.log(error)
        this.$router.go(-1)
      })
    })

    //listners
    const element = this.$refs.historic
    element.addEventListener('scroll', () => {
      if((element.scrollTop + element.clientHeight) >= element.scrollHeight){
        if(!this.searchFlag){
          this.flag2 = false
          if(this.flag1){
            this.loadHistoric()
          }
        }
      }
    })

    const element2 = window
    element2.addEventListener('scroll', () => {
      const visible = document.documentElement.clientHeight
      const pageHeight = document.documentElement.scrollHeight
      if((visible + element2.scrollY) >= pageHeight){
        if(!this.searchFlag){
          this.flag1 = false
          if(this.flag2){
            this.loadHistoric()
          }
        }
      }
    })

    //responsible div
    const element3 = this.$refs.historic2
    element3.addEventListener('scroll', () => {
      if((element3.scrollTop + element3.clientHeight) >= element3.scrollHeight){
        if(!this.searchFlag){
          this.flag2 = false
          if(this.flag1){
            this.loadHistoric()
          }
        }
      }
    })
  },
  beforeDestroy () {
    window.removeEventListener('scroll', () => {})
    this.$refs.historic.removeEventListener('scroll', () => {})
    this.$refs.historic2.removeEventListener('scroll', () => {})
  }
}
</script>

