    margin-right: 5px;
  }

  .filter-box .calendar{
    position: absolute;
    right: 20px;
    height: 14px;
    width: auto;
    top: 60%;
    cursor: pointer;
    background-color: #fff;
  }

  .filter-box .date-box{
    width: 250px;
    height: auto;
    padding: 15px 10px;
    background-color: #fff;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    position: absolute;
    left: 30px;
    top: 80%;
  }

  .date-box .item{
    display: inline-block;
    height: 53px;
    width: 53px;
    line-height: 53px;
    border: 1px solid #ACACAC;
    color: #000000;
    text-align: center;
    cursor: pointer;
    margin: 2px 2.1px;
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
    -ms-transition: all .3s;
    -o-transition: all .3s;
  }

  .date-box .item:hover,
  .date-box .item.active{
    background-color: #E60965;
    color: #fff;
  }

  .my-table{
    width: 100%;
    height: auto;
  }

  .my-table .header{
    background-color: #fff;
    border-bottom: 1px solid rgba(11, 32, 39, 0.15);
    margin-top: 20px;
    padding: 15px 35px;
    color: #67676744;
    font-size: 15px;
  }

  .my-table .body {
    max-height: 50vh;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }

  .my-table .body::-webkit-scrollbar {
    width: 5px;
  }

  /* Track */
  .my-table .body::-webkit-scrollbar-track {
    background: #EFEFEF;
  }

  /* Handle */
  .my-table .body::-webkit-scrollbar-thumb {
    background: transparent;
    border: 1px solid #888;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                      0 1px 5px 0 rgba(0, 0, 0, 0.12),
                      0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
              0 1px 5px 0 rgba(0, 0, 0, 0.12),
              0 3px 1px -2px rgba(0, 0, 0, 0.2);
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
    -ms-transition: all .3s;
    -o-transition: all .3s;
    border-radius: 12px;
  }

  /* Handle on hover */
  .my-table .body::-webkit-scrollbar-thumb:hover {
    border-color: #555;
  }

  .my-table .body .item{
    padding: 15px 35px;
    font-size: 15px;
    color: rgba(11, 32, 39, 0.7);
  }

  .my-table .body .item:nth-child(odd){
    background-color: #fff;
  }

  .my-table .body .item:nth-child(even){
    background-color: #EFEFEF;
  }

  .arrow2{
    height: 26px;
    width: auto;
    cursor: pointer;
  }

  @media only screen and (max-width: 992px){
    .pic{
      height: 110px;
      width: 110px;
    }

    .edit-pic{
      height: 24px;
      width: 24px;
    }

    .change-pet{
      font-size: 10px;
    }

    .pet .pet-drop .name{
      font-size: 18px;
    }

    .pet .id,
    .remove-pet{
      font-size: 10px;
    }

    .pet .info .title{
      font-size: 10px;
    }

    .pet .info .value{
      font-size: 12px;
    }

    .res-table{
      width: 100%;
      max-height: 850px;
      overflow-y: auto;
      padding-top: 10px;
      padding-bottom: 10px;
    }

    .res-table .item{
      width: 380px;
      height: auto;
      padding: 10px 20px;
      border: 1px solid #676767;
      display: block;
      margin: 0 auto 15px auto;
      color: #676767af;
      font-family: Lato-regular;
      font-size: 15px;
    }

    .res-table .item .tt{
      color: #6767674b;
    }

    .res-table .item .row{
      padding-bottom: 10px;
      padding-top: 10px;
      border-bottom: 1px solid rgba(11, 32, 39, 0.1);
    }

    .res-table .item .row:last-child{
      border-bottom: none;
    }
  }

  @media only screen and (max-width: 767.98px){
    .pet{
      height: auto;
      padding-bottom: 30px;
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%)
    }

    .pet .info{
      padding-right: 0;
      padding-left: 0;
    }

    .medical-history{
      padding-top: 30px;
    }

    .remove-pet.text-right{
      position: static;
      text-align: center !important;
    }

    .medical-history h3{
      text-align: center;
    }

    .btn-filter{
      display: block;
      margin: 15px auto;
    }

    .filter-box{
      width: 100%;
    }

    .filter-box .btn{
      display: block;
      margin: 15px auto 0 auto;
    }

    .filter-box .date-box{
      right: 20px;
      left: inherit;
      top: 90px;
      z-index: 2;
    }

    .filter-box label{
      font-size: 12px;
      margin-top: 10px;
    }
  }

  @media only screen and (max-width: 575.98px){
    .pet .info .title,
    .pet .info .value{
      text-align: center;
    }

    .res-table .item{
      width: 100%;
      padding: 10px 15px;
    }
  }

  @media only screen and (max-width: 375px){
    .filter-box .date-box{
      right: 50%;
      transform: translateX(50%);
      -moz-transform: translateX(50%);
      -webkit-transform: translateX(50%);
      -ms-transform: translateX(50%);
      -o-transform: translateX(50%);
    }
  }
</style>



