<template>
  <div class="page">
    <app-client-header></app-client-header>
    <div class="container-fluid my-container">
      <div class="profile-box">
        <div class="row">
          <div class="col-sm col-12">
            <p class="name">{{user.nome}}</p>
          </div>
          <div class="col-sm-auto col-12">
            <label class="xx" for="profile-pic">
              <img :src="(user.img) ? user.img : 'static/imgs/default_pet.svg'" alt="" class="profile">
            </label>
            <input id="profile-pic" type="file" @change="handleFileUpload">
          </div>
        </div>
        <form @submit.prevent="submit">
          <div class="row">
            <div class="col-sm-12 col-12">
              <label for="">CPF</label>
              <p class="cpf">{{this.user.cpf}}</p>
            </div>
            <div class="col-sm-8 col-12">
              <label for="">E-mail</label>
              <app-email-input v-model.trim="dataUser.email.model" :data="dataUser.email" className="wbg disabled"></app-email-input>
            </div>
            <div class="col-sm-4 col-12">
              <label for="">Telefone</label>
              <app-text-input v-model.trim="dataUser.phone.model" :data="dataUser.phone" className="wbg"></app-text-input>
            </div>
            <div class="col-sm-9 col-6">
              <label for="">CEP</label>
              <app-text-input mask="cep" elId="cep" v-model.trim="dataUser.cep.model" :data="dataUser.cep" className="wbg"></app-text-input>
            </div>
            <div class="col-sm-3 col-6">
              <label for="">N°</label>
              <app-text-input elId="addressNumber" mask="addressNumber" v-model.trim="dataUser.number.model" :data="dataUser.number" className="wbg"></app-text-input>
            </div>
            <div class="col-sm-12 col-12">
              <label for="">Rua</label>
              <app-text-input v-model.trim="dataUser.street.model" :data="dataUser.street" className="wbg"></app-text-input>
            </div>
            <div class="col-sm-12 col-12">
              <label for="">Bairro</label>
              <app-text-input v-model.trim="dataUser.neighborhood.model" :data="dataUser.neighborhood" className="wbg"></app-text-input>
            </div>
            <div class="col-sm-12 col-12">
              <label for="">Complemento</label>
              <app-text-input v-model.trim="dataUser.complement.model" :data="dataUser.complement" className="wbg"></app-text-input>
            </div>
            <div class="col-sm-9 col-12">
              <label for="">Cidade</label>
              <app-text-input v-model.trim="dataUser.city.model" :data="dataUser.city" className="wbg"></app-text-input>
            </div>
            <div class="col-sm-3 col-6">
              <label for="">UF</label>
              <app-select-input elId="uf" v-model="dataUser.uf.model" :options="stateList" :placeholder="dataUser.uf.model" :data="dataUser.uf"></app-select-input>
            </div>
            <div class="col-auto">
              <router-link to="/cancelar-plano">
                <p class="cancel">Cancelar meu plano Iopet</p>
              </router-link>
            </div>
            <div class="col text-right">
              <button type="submit" class="btn btn-send">salvar</button>
            </div>
          </div>
        </form>
      </div>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import axios from '@/auth'
import TextInput from '@/components/inputs/TextInput'
import SelectInput from '@/components/inputs/SelectInput'
import EmailInput from '@/components/inputs/EmailInput'
import EventBus from '@/main'
import Footer from '@/components/footer/Footer'
import ClientHeader from '@/components/header/ClientHeader'
export default {
  name: 'ProfilePage',
  data () {
    return {
      user: {},
      dataUser: {},
      stateList: [
        {name: 'AC', value: 'AC'},
        {name: 'AL', value: 'AL'},
        {name: 'AP', value: 'AP'},
        {name: 'AM', value: 'AM'},
        {name: 'BA', value: 'BA'},
        {name: 'CE', value: 'CE'},
        {name: 'DF', value: 'DF'},
        {name: 'ES', value: 'ES'},
        {name: 'GO', value: 'GO'},
        {name: 'MA', value: 'MA'},
        {name: 'MT', value: 'MT'},
        {name: 'MS', value: 'MS'},
        {name: 'MG', value: 'MG'},
        {name: 'PA', value: 'PA'},
        {name: 'PB', value: 'PB'},
        {name: 'PR', value: 'PR'},
        {name: 'PE', value: 'PE'},
        {name: 'PI', value: 'PI'},
        {name: 'RJ', value: 'RJ'},
        {name: 'RN', value: 'RN'},
        {name: 'RS', value: 'RS'},
        {name: 'RO', value: 'RO'},
        {name: 'RR', value: 'RR'},
        {name: 'SC', value: 'SC'},
        {name: 'SP', value: 'SP'},
        {name: 'SE', value: 'SE'},
        {name: 'TO', value: 'TO'}
      ]
    }
  },
  components: {
    appFooter: Footer,
    appClientHeader: ClientHeader,
    appTextInput: TextInput,
    appEmailInput: EmailInput,
    appSelectInput: SelectInput
  },
  methods: {
    validate () {
      if(this.dataUser.email.model === ''){
        this.dataUser.email.error.error = true
      }
      if(this.dataUser.phone.model === ''){
        this.dataUser.phone.error.error = true
      }
      if(this.dataUser.cep.model === ''){
        this.dataUser.cep.error.error = true
      }
      else if(this.dataUser.cep.model < 10){
          this.dataUser.cep.error.error = true
          this.dataUser.cep.error.msg = 'CEP inválido'
        }
      if(this.dataUser.number.model === ''){
        this.dataUser.number.error.error = true
      }
      if(this.dataUser.street.model === ''){
        this.dataUser.street.error.error = true
      }
      if(this.dataUser.neighborhood.model === ''){
        this.dataUser.neighborhood.error.error = true
      }
      if(this.dataUser.city.model === ''){
        this.dataUser.city.error.error = true
      }
      if(this.dataUser.uf.model === ''){
        this.dataUser.uf.error.error = true
      }
      let error = false
      for(let key in this.dataUser){
        const element = this.dataUser[key]
        if(element.error.error){
          error = true
        }
      }
      return error
    },
    submit () {
      const error = this.validate()
      if(!error){
        const formData = {
          email: this.dataUser.email.model,
          telefone: this.dataUser.phone.model,
          cep: this.dataUser.cep.model,
          numero: this.dataUser.number.model,
          logradouro: this.dataUser.street.model,
          bairro: this.dataUser.neighborhood.model,
          municipio: this.dataUser.city.model,
          estado: this.dataUser.uf.model,
          complemento: this.dataUser.complement.model
        }
        EventBus.$emit('loadingPageCallback', true)
        axios.post('/atualizar/conta', formData)
        .then(res => {
          EventBus.$emit('loadingPageCallback', false)
          location.reload()
        })
        .catch(error => {
          console.log(error)
        })
      }
    },
    handleFileUpload(e) {
      let formData = new FormData()
      formData.append('imagem', e.target.files[0])
      axios.post('/imagem/usuario', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
      .then(res => {
        location.reload()
      })
      .catch(error => {
        console.log(error)
      })
    }
  },
  created () {
    this.dataUser = {
      email: {
        error: {
          error: false,
          msg: 'Campo obrigatório'
        },
        model: ''
      },
      name: {
        error: {
          error: false,
          msg: 'Campo obrigatório'
        },
        model: ''
      },
      phone: {
        error: {
          error: false,
          msg: ''
        },
        model: ''
      },
      cep: {
        error: {
          error: false,
          msg: 'Campo obrigatório'
        },
        model: ''
      },
      number: {
        error: {
          error: false,
          msg: 'Campo obrigatório'
        },
        model: ''
      },
      street: {
        error: {
          error: false,
          msg: 'Campo obrigatório'
        },
        model: ''
      },
      complement: {
        error: {
          error: false,
          msg: 'Campo obrigatório'
        },
        model: ''
      },
      neighborhood: {
        error: {
          error: false,
          msg: 'Campo obrigatório'
        },
        model: ''
      },
      city: {
        error: {
          error: false,
          msg: 'Campo obrigatório'
        },
        model: ''
      },
      uf: {
        error: {
          error: false,
          msg: 'Campo obrigatório'
        },
        model: ''
      },
    }
  },
  mounted() {
    this.$store.dispatch('fetchUser').then(() =>{
      this.user = JSON.parse(localStorage.getItem('user'))
      axios.get('/conta')
      .then(res => {
        const data = res.data.user
        this.user.img = data.foto
        this.dataUser.email.model = data.email
        this.dataUser.phone.model = data.telefone
        this.dataUser.cep.model = data.endereco.cep
        this.dataUser.complement.model = data.endereco.complemento
        this.dataUser.number.model = data.endereco.numero
        this.dataUser.street.model = data.endereco.logradouro
        this.dataUser.neighborhood.model = data.endereco.bairro
        this.dataUser.city.model = data.endereco.municipio
        this.dataUser.uf.model = data.endereco.estado
        EventBus.$emit('loadingPageCallback', false)
      })
      .catch(error => {
        console.log(error)
      })
    })
  }
}
</script>

<style scoped>
  .page{
    padding-top: 130px;
    background-image: url(/static/imgs/profile_bg.jpg);
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .container-fluid{
    min-height: 100vh;
  }

  .profile-box{
    display: block;
    margin: 30px auto;
    background-color: #FFFFFF;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    width: 100%;
    max-width: 530px;
    padding: 30px;
  }

  .profile{
    width: 130px;
    height: 130px;
    border-radius: 50%;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    cursor: pointer;
  }

  .name{
    color: #676767;
    font-size: 42px;
    font-family: ubuntu-medium;
    line-height: 40px;
    margin-top: 25px;
  }

  .cpf{
    max-width: 160px;
    background-color: #fff;
    border: none;
    border: 1px solid #707070;
    border-radius: 0;
    outline: none;
    font-size: 14px;
    margin: 0 0 20px 0;
    padding: 10px 15px;
    width: 100%;
    -webkit-box-shadow: none;
            box-shadow: none;
    -webkit-transition: all 0.3s;
    -moz-webkit-transition: all 0.3s;
    -ms-webkit-transition: all 0.3s;
    -o-webkit-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
  }

  label{
    color: #505050;
  }

  .cancel{
    color: #E60965;
    font-size: 13px;
  }

  #profile-pic{
    display: none;
  }

  @media only screen and (max-width: 767.98px){
    label{
      font-size: 12px;
    }
  }

  @media only screen and (max-width: 575.98px){
    .name{
      font-size: 26px;
      text-align: center;
    }

    .profile{
      height: 80px;
      width: 80px;
      display: block;
      margin: 0 auto 15px auto;
    }

    .xx{
      display: block;
    }

    .profile-box{
      padding: 15px;
    }
  }
</style>



