            <img v-if="newCard.flag.model==='Visa'" class="visa" src="static/imgs/visa.svg" alt="">
            <img v-if="newCard.flag.model==='Mastercard'" class="master" src="static/imgs/mastercard.svg" alt="">
            <img v-if="newCard.flag.model==='Diners'" class="diners" src="static/imgs/diners.svg" alt="">
            <img v-if="newCard.flag.model==='Elo'" class="elo" src="static/imgs/elo.svg" alt="">
            <img v-if="newCard.flag.model==='Hiper'" class="hiper" src="static/imgs/hiper.svg" alt="">
