              <app-text-input mask="cardDate" elId="cdate" v-model="newCard.name.date" :data="newCard.date" placeholder="Data de vencimento" className="wbg"></app-text-input>
            </div>
            <div class="col-6 col-sm-12 col-12">
              <app-text-input mask="cardCode" elId="ccode" v-model="newCard.name.code" :data="newCard.code" placeholder="CVC" className="wbg"></app-text-input>
            </div>
            <div class="col-12">
              <input v-model="newCard.holder.model" type="checkbox" class="form-check-input" id="titular">
              <label class="form-check-label" for="titular">Sou o titular do cartão</label>
