    <!--add-card-->

    <!--addConfModal-->
    <transition name="fade" mode="out-in">
      <div v-if="addConfModal" class="my-modal add-conf">
        <div class="header">
          <p>Seu cartão foi adicionado <br> com sucesso!</p>
        </div>
        <div class="text-center mt15 row">
          <div class="col-12">
            <app-card :add="true" type="card" :card="confNewCard"></app-card>
          </div>
          <div class="col-12 text-center">
            <button @click="addConfModal = false, overlay = false, cleanCard(), loadCards()" class="btn btn-send">ok</button>
          </div>
        </div>
      </div>
    </transition>
    <!--modal-->

    <app-client-header></app-client-header>
    <div class="container-fluid my-container">
      <section class="invoices-cards">
        <div class="header">
          <div class="border">
            <div :class="{offset: box===2}" class="active"></div>
          </div>
          <div class="row">
            <div class="col-auto">
              <p @click="box=1" :class="{active: box===1}">Faturas</p>
            </div>
            <div class="col-auto">
              <p @click="box=2" :class="{active: box===2}">Cartões</p>
            </div>
            <div class="col text-right">
              <transition name="fade" mode="out-in">
                <span v-if="box===1">
                  <img @click="filter = !filter" class="ico" src="static/imgs/f_filter.svg" alt="">
                </span>
              </transition>
              <transition name="fade" mode="out-in">
                <div v-if="filter" class="search-box">
                  <div class="row">
                    <div class="col-12">
                      <p class="title2">Período</p>
                    </div>
                    <div class="col-12">
                      <app-select-input v-model="searchForm.in.model" :options="months" elId="de" :data="searchForm.in" placeholder="De"></app-select-input>
                    </div>
                    <div class="col-12">
                      <app-select-input v-model="searchForm.up.model" :options="months" elId="de" :data="searchForm.up" placeholder="Até"></app-select-input>
                    </div>
                    <div class="col-12">
                      <p class="title2">Ano</p>
                    </div>
                    <div class="col-12">
                      <input class="wbg" type="text" v-model="searchForm.year.model">
