                  <div v-for="(item, index) in invocesList" :key="index" class="item">
                    <div class="row">
                      <div class="col"><span>{{competence(item.competence)}}</span></div>
                      <div class="col"><span>{{dateFormat(item.maturity)}}</span></div>
                      <div class="col text-center"><span>R$ {{numberFormat(item.value/100,2,',','.')}}</span></div>
                      <div class="col text-center">
                        <span
                          :class="{pending: getStatus(item.status) === 'Pendente', arrears: getStatus(item.status) === 'Em atraso', paid: getStatus(item.status) === 'Paga'}"
                          class="status">{{getStatus(item.status)}}
                        </span>
                        </div>
                      <div class="col-auto"><button  class="btn btn-send">Enviar por email</button></div>
                    </div>
                  </div>
                </div>
              </span>
              <div ref="invoicesMob" key="invoices-mob" class="invoices-mob mob">
                <div v-for="(item, index) in invocesList" :key="index" class="item">
                  <div class="row">
                    <div class="col">
                      <span class="title">Competência</span>
                    </div>
                    <div class="col">
                      <span class="desc">
                        {{competence(item.competence)}}
                      </span>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col">
                      <span class="title">Vencimento</span>
