                    <div class="col">
                      <span class="desc">
                        {{dateFormat(item.maturity)}}
                      </span>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col">
                      <span class="title">Valor</span>
                    </div>
                    <div class="col">
                      <span class="desc">
                        R$ {{numberFormat(item.value/100,2,',','.')}}
                      </span>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col">
                      <span class="title">Status</span>
                    </div>
                    <div class="col">
                      <span
                        :class="{pending: getStatus(item.status) === 'Pendente', arrears: getStatus(item.status) === 'Em atraso', paid: getStatus(item.status) === 'Paga'}"
                        class="status">{{getStatus(item.status)}}
                      </span>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col text-center">
                      <button  class="btn btn-send">Enviar por email</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div v-if="box === 2" key="cards" class="cards">
              <div class="row justify-content-start" is="transition-group" name="fade" mode="out-in">
                <div v-for="(item, index) in cardList" :key="index" class="col-lg-3 col-md-6 col-sm-6 col-12 align-self-center">
                  <app-card @details="showDetails(index)" @selectCard="setCard(index)" :card="item" type="card"></app-card>
                </div>
                <div key="new" class="col-lg-3 col-md-6 col-sm-6 col-12 align-self-center">
                  <app-card @newCard="addCard" type="new"></app-card>
                </div>
              </div>
            </div>
          </transition>
        </div>
      </section>
