  </div>
</template>

<script>
import axios from '@/auth'
import TextInput from '@/components/inputs/TextInput'
import EventBus from '@/main'
import Card from '@/components/cards/Card'
import ClientHeader from '@/components/header/ClientHeader'
import Spinner from '@/components/spinner/Spinner'
import SelectInput from '@/components/inputs/SelectInput'
export default {
  name: 'FinancialPage',
  data () {
    return {
      searchForm: {
        in: {
          error: {
            error: false,
            msg: 'Campo obrigatório'
          },
          model: null
        },
        up: {
          error: {
            error: false,
            msg: 'Campo obrigatório'
          },
          model: null
        },
        year: {
          error: {
            error: false,
            msg: 'Campo obrigatório'
          },
          model: ''
        }
      },
      months: [
        {name: 'Janeiro', value: 1},
        {name: 'Fevereiro', value: 2},
        {name: 'Março', value: 3},
        {name: 'Abril', value: 4},
        {name: 'Maio', value: 5},
        {name: 'Junho', value: 6},
        {name: 'Julho', value: 7},
        {name: 'Agosto', value: 8},
        {name: 'Setembro', value: 9},
        {name: 'Outubro', value: 10},
        {name: 'Novembro', value: 11},
        {name: 'Dezembro', value: 12}
      ],
      filter: false,
      searchFlag: false,
      flag1: true,
      flag2: true,
      user: {},
      confCard: {},
      box: 1,
      overlay: false,
      detailsModal: false,
      removeConfModal: false,
      addCardModal: false,
      addConfModal: false,
      newCard: {},
      confNewCard: {},
      selectedCard: {},
      cardList: [],
      invocesList: []
    }
  },
  components: {
    appClientHeader: ClientHeader,
    appCard: Card,
    appTextInput: TextInput,
    appSpinner: Spinner,
    appSelectInput: SelectInput
  },
  methods: {
    search () {
      EventBus.$emit('loadingPageCallback', true)
      const formData = {
        de: this.searchForm.in.model,
        ate: this.searchForm.up.model,
        ano: this.searchForm.year.model
      }
      axios.post('/filtrar/faturas', formData)
      .then(res => {
        console.log(res)
        const data = res.data.filtro
        const invoices = []
        for(let key in data){
          const invoice = {
            id: data[key].id,
            competence: data[key].competencia,
            plan: data[key].plano_id,
            status: data[key].status,
            value: data[key].valor,
            maturity: data[key].vencimento
          }
          invoices.push(invoice)
        }
        this.invocesList = invoices
        this.filter = false
        this.searchFlag = true
        EventBus.$emit('loadingPageCallback', false)
      })
      .catch(error => {
        this.filter = false
        EventBus.$emit('loadingPageCallback', false)
        console.log(error)
      })
    },
    removeCard () {
      EventBus.$emit('loadingPageCallback', true)
      axios.get('/delete/card/'+this.selectedCard.id)
      .then(res => {
        this.cardList.splice(this.selectedCard.index,1)
        this.removeConfModal = false
        this.overlay = false
        EventBus.$emit('loadingPageCallback', false)
      })
      .catch(error => {
        console.log(error)
      })
    },
    cleanCard () {
      this.newCard = {
        flag: {
          error: {
              error: false,
              msg: 'Número incorreto'
            },
          model: ''
        },
        number: {
          error: {
              error: false,
              msg: 'Número incorreto'
            },
          model: ''
        },
        name: {
          error: {
              error: false,
              msg: 'Campo obrigatório'
            },
          model: ''
        },
        date: {
          error: {
              error: false,
              msg: 'Campo obrigatório'
            },
          model: ''
        },
        code: {
          error: {
              error: false,
              msg: 'Campo obrigatório'
            },
          model: ''
        },
        holder: {
          error: {
              error: false,
              msg: 'Campo obrigatório'
            },
          model: 1
        },
        cpf: {
          error: {
              error: false,
              msg: 'Campo obrigatório'
            },
          model: null
        },
        holderName: {
          error: {
              error: false,
              msg: 'Campo obrigatório'
            },
          model: null
        },
        birth: {
          error: {
              error: false,
              msg: 'Campo obrigatório'
            },
          model: null
        },
        phone: {
          error: {
              error: false,
              msg: 'Campo obrigatório'
            },
          model: null
        }
      }
    },
    loadCards () {
      axios.get('/resumo/cards')
      .then(res => {
        const data = res.data.cards
        const cards = []
        for(let key in data){
          const card = {
            user: this.user.nome,
            pay: data[key].data,
            flag: data[key].bandeira,
            number: data[key].digitos,
            active: data[key].padrao,
            status: data[key].status,
            id: data[key].id,
            hash: data[key].hash
          }
          cards.push(card)
        }
        this.cardList = cards
        EventBus.$emit('loadingPageCallback', false)
      })
      .catch(error => {
        console.log(error)
      })
    },
    showDetails (index) {
      this.overlay = true
      this.detailsModal = true
      this.selectedCard = this.cardList[index]
      this.selectedCard.index = index
    },
    addCard () {
      this.overlay = true
      this.addCardModal = true
    },
    validateCard () {
      const date = new Date()
      const thisYear = date.getFullYear()
      const [first, ... year] = this.newCard.date.model.split('/')
      if(year < thisYear){
        this.newCard.date.error.error = true
        this.newCard.date.error.msg = 'Data inválida'
      }
      if(this.newCard.number.flag === ''){
        this.newCard.flag.error.error = true
      }
      if(this.newCard.number.model === ''){
        this.newCard.number.error.error = true
      }
      else if(this.newCard.number.model.length < 18){
        this.newCard.number.error.error = true
        this.newCard.number.error.msg = 'Número inválido'
      }
      if(this.newCard.name.model === ''){
        this.newCard.name.error.error = true
      }
      if(this.newCard.date.model === ''){
        this.newCard.date.error.error = true
      }
      else if(this.newCard.date.model < 7){
        this.newCard.date.error.error = true
        this.newCard.date.error.msg = 'Data inválida'
      }
      if(this.newCard.code.model === ''){
        this.newCard.code.error.error = true
      }
      else if(this.newCard.code.model < 3){
        this.newCard.code.error.error = true
        this.newCard.code.error.msg = 'Código inválido'
      }
      if(!this.newCard.holder.model){
        if(this.newCard.holderName.model === ''){
          this.newCard.holderName.error.error = true
        }
        if(this.newCard.cpf.model === ''){
          this.newCard.cpf.error.error = true
        }
        else if(this.newCard.cpf.model.length < 14){
          this.newCard.cpf.error.error = true
          this.newCard.cpf.error.msg = 'CPF inválido'
        }
        if(this.newCard.birth.model === ''){
          this.newCard.birth.error.error = true
        }
        else if(this.newCard.birth.model.length < 10){
          this.newCard.birth.error.error = true
          this.newCard.birth.error.msg = 'Data inválida'
        }
        if(this.newCard.phone.model === ''){
          this.newCard.phone.error.error = true
        }
      }
      let error = false
      for(let key in this.newCard){
        if(this.newCard[key].error.error === true)
          error = true
      }
      return error
    },
    submitCard () {
      const error = this.validateCard()
      if(!error){
        if(this.newCard.holder){
          const formData = {
            bandeira: this.newCard.flag.model,
            numero: this.newCard.number.model,
            nome: this.newCard.name.model,
            data: this.newCard.date.model,
            cvc: this.newCard.code.model,
            sou_titular: this.newCard.holder.model
          }
          EventBus.$emit('loadingPageCallback', true)
          axios.post('/adicionar/cards', formData)
          .then(res => {
            const card = res.data.card
            this.confNewCard = {
              flag: card.bandeira,
              number: card.digitos,
              name: card.nome,
              date: card.data,
              active: card.padrao,
              status: card.status
            }
            this.addCardModal = false
            this.addConfModal = true
            EventBus.$emit('loadingPageCallback', false)
          })
          .catch(error => {
            console.log(error)
          })
        }
        else{
          const formData = {
            bandeira: this.newCard.flag.model,
            numero: this.newCard.number.model,
            nome: this.newCard.name.model,
            data: this.newCard.date.model,
            cvc: this.newCard.code.model,
            sou_titular: this.newCard.holder.model,
            cpf: this.newCard.cpf.model,
            telefone: this.newCard.phone.model,
            nome: this.newCard.name.model,
            nascimento: this.newCard.date.model
          }
          EventBus.$emit('loadingPageCallback', true)
          axios.post('/adicionar/cards', formData)
          .then(res => {
            const card = res.data.card
            this.confNewCard = {
              flag: card.bandeira,
              number: card.digitos,
              name: card.nome,
              date: card.data,
              active: card.padrao,
              status: card.status
            }
            this.addCardModal = false
            this.addConfModal = true
            EventBus.$emit('loadingPageCallback', false)
          })
          .catch(error => {
            console.log(error)
          })
        }
      }
    },
    setCard (index) {
      EventBus.$emit('loadingPageCallback', true)
      axios.get('/default/card/'+this.cardList[index].id)
      .then(res => {
        this.cardList.forEach(card => {
          card.active = 0
        })
        this.cardList[index].active = 1
        EventBus.$emit('loadingPageCallback', false)
      })
      .catch(error => {
        console.log(error)
      })
    },
    GetCardType (number) {
      // visa
      var re = new RegExp("^4")
      if (number.match(re) != null){
        console.log('Visa')
        return 'Visa'
      }

      // Mastercard
      // Updated for Mastercard 2017 BINs expansion
      if (/^(5[1-5][0-9]{14}|2(22[1-9][0-9]{12}|2[3-9][0-9]{13}|[3-6][0-9]{14}|7[0-1][0-9]{13}|720[0-9]{12}))$/.test(number)){
        console.log('Mastercard')
        return 'Mastercard'
      }

      // AMEX
      re = new RegExp("^3[47]")
      if (number.match(re) != null){
        console.log('AMEX')
        return 'AMEX'
      }

      // Discover
      re = new RegExp("/^6(?:011|5[0-9]{2})/")
      if (number.match(re) != null){
        console.log('Discover')
        return 'Discover'
      }

      // Diners
      re = new RegExp("/^3(?:0[0-5]|[68][0-9])/")
      if (number.match(re) != null){
        console.log('Diners')
        return 'Diners'
      }

      // Diners - Carte Blanche
      re = new RegExp("^30[0-5]")
      if (number.match(re) != null){
        console.log('Diners')
        return 'Diners'
      }

      // JCB
      re = new RegExp("^35(2[89]|[3-8][0-9])")
      if (number.match(re) != null){
        console.log('JCB')
        return 'JCB'
      }

      // Visa Electron
      re = new RegExp("^(4026|417500|4508|4844|491(3|7))")
      if (number.match(re) != null){
        console.log('Visa Electron')
        return 'Visa Electron'
      }

	  // ELO
      re = new RegExp("/^401178|^401179|^431274|^438935|^451416|^457393|^457631|^457632|^504175|^627780|^636297|^636368|^(506699|5067[0-6]\d|50677[0-8])|^(50900\d|5090[1-9]\d|509[1-9]\d{2})|^65003[1-3]|^(65003[5-9]|65004\d|65005[0-1])|^(65040[5-9]|6504[1-3]\d)|^(65048[5-9]|65049\d|6505[0-2]\d|65053     [0-8])|^(65054[1-9]|6505[5-8]\d|65059[0-8])|^(65070\d|65071[0-8])|^65072[0-7]|^(65090[1-9]|65091\d|650920)|^(65165[2-9]|6516[6-7]\d)|^(65500\d|65501\d)|^(65502[1-9]|6550[3-4]\d|65505[0-8])/")
      if (number.match(re) != null){
        console.log('ELO')
        return 'ELO'
      }

	  // hipercard
      re = new RegExp("/^3841[046]0|^60/")
      if (number.match(re) != null){
        console.log('hipercard')
        return 'Hipercard'
      }

	  // aura
      re = new RegExp("/^50[0-9]/")
      if (number.match(re) != null){
        console.log('aura')
        return 'Aura'
      }

      return 'Não encontrado'
    },
    getStatus (status) {
      if (status === 'PAGA'){
        return 'Paga'
      }
      else if (status === 'PENDENTE'){
        return 'Pendente'
      }
      else if (status === 'ATRASADA'){
        return 'Em atraso'
      }
    },
    loadInvoices () {
      let lastId
      if(this.invocesList.length > 0) {
        lastId = this.invocesList[this.invocesList.length-1].id
      }
      else{
        lastId = ''
      }
      axios.get('/faturas/?registro_id='+lastId)
      .then(res => {
        const data = res.data.financeiro
        for(let key in data){
          const invoice = {
            id: data[key].id,
            competence: data[key].competencia,
            plan: data[key].plano_id,
            status: data[key].status,
            value: data[key].valor,
            maturity: data[key].vencimento
          }
          this.invocesList.push(invoice)
        }
        this.flag1 = true
        this.flag2 = true
      })
      .catch(error => {
        console.log(error)
      })
    }
  },
  created () {
    this.$store.dispatch('fetchUser').then(() =>{
      this.user = JSON.parse(localStorage.getItem('user'))
      axios.get('/faturas/?registro_id='+1)
      .then(res => {
        const data = res.data.financeiro
        const invoices = []
        for(let key in data){
          const invoice = {
            id: data[key].id,
            competence: data[key].competencia,
            plan: data[key].plano_id,
            status: data[key].status,
            value: data[key].valor,
            maturity: data[key].vencimento
          }
          invoices.push(invoice)
        }
        this.invocesList = invoices
        this.loadCards()
      })
      .catch(error => {
        console.log(error)
      })
    })
  },
  mounted () {
    this.cleanCard()
    const element = this.$refs.invoices
    element.addEventListener('scroll', () => {
      if((element.scrollTop + element.clientHeight) >= element.scrollHeight){
        if(!this.searchFlag){
          this.flag2 = false
          if(this.flag1){
            this.loadInvoices()
          }
        }
      }
    })

    const element3 = this.$refs.invoicesMob
    element3.addEventListener('scroll', () => {
      if((element3.scrollTop + element3.clientHeight) >= element3.scrollHeight){
        if(!this.searchFlag){
          this.flag2 = false
          if(this.flag1){
            this.loadInvoices()
          }
        }
      }
    })

    const element2 = window
    element2.addEventListener('scroll', () => {
      const visible = document.documentElement.clientHeight
      const pageHeight = document.documentElement.scrollHeight
      if((visible + element2.scrollY) >= pageHeight){
        if(!this.searchFlag){
          this.flag1 = false
          if(this.flag2){
            this.loadInvoices()
          }
        }
      }
    })
  },
  beforeDestroy () {
    this.$refs.invoices.removeEventListener('scroll', () => {})
    this.$refs.invoicesMob.removeEventListener('scroll', () => {})
    window.removeEventListener('scroll', () => {})
  },
  watch: {
    newCard: {
      handler(val){
        this.newCard.flag.model = this.GetCardType(val.number.model)
      },
      deep: true
    }
  }
}
</script>

