<template>
  <div class="page">
    <app-quotation-header></app-quotation-header>
    <div class="container-fluid my-container">
      <div class="row">
        <div class="col">
          <p class="title">Vamos começar sabendo sobre o seu pet!</p>
        </div>
      </div>
      <form @submit.prevent="submit">
        <div class="row">
          <div class="col-md col-sm-12 col-12">
            <label for="">Data de nascimento</label>
            <app-text-input className="wbg" v-model="pet.birth.model" :data="pet.birth" elId="petDate" mask="date"></app-text-input>
          </div>
          <div class="col-md col-sm-12 col-12">
            <label for="">Espécie</label>
            <app-specie-input
              elId="specie"
              placeholder="Escolha a espécie"
              :data="pet.specie"
              v-model="pet.specie.model">
            </app-specie-input>
          </div>
          <div class="col-md col-sm-12 col-12">
            <label for="">Raça</label>
            <app-breed-input
              elId="breed"
              placeholder="Escolha a raça"
              :data="pet.breed"
              v-model="pet.breed.model">
            </app-breed-input>
          </div>
        </div>
        <div class="row">
          <div class="col-md col-sm-12 col-12">
            <label for="">Porte</label>
            <app-shape-input
              elId="shape"
              placeholder="Escolha o porte"
              :data="pet.shape"
              v-model="pet.shape.model">>
            </app-shape-input>
          </div>
          <div class="col-md col-sm-12 col-12">
            <label for="">Peso (em kg)</label>
            <app-text-input mask="decimals" className="wbg" v-model="pet.weight.model" :data="pet.weight" elId="petWeight"></app-text-input>
          </div>
          <div class="col-md col-sm-12 col-12">
            <label for="">Sexo</label>
            <app-select-input elId="sex" v-model="pet.sex.model" :options="[{name:'Masculino', value:'Masculino'},{name:'Feminino', value:'Feminino'}]" placeholder="Sexo" :data="pet.sex"></app-select-input>
          </div>
        </div>
        <div class="line"></div>
        <div class="row">
          <div class="col-md col-sm-12 col-12">
            <label for="">Animal resgatado?</label>
            <p>
              <input @change="pet.rescued.error.error = false" value="1" class="with-gap" v-model="pet.rescued.model" name="resg" type="radio" id="resgs"/>
              <label for="resgs">Sim</label>
              <input @change="pet.rescued.error.error = false" value="0" class="with-gap" v-model="pet.rescued.model" name="resg" type="radio" id="resgn"/>
              <label class="offset" for="resgn">Não</label>
            </p>
            <transition name="fade" mode="out-in">
              <p v-if="pet.rescued.error.error" class="msg-error">{{pet.rescued.error.msg}}</p>
            </transition>
          </div>
        </div>
        <div class="line"></div>
        <div class="row">
          <div class="col-md col-sm-12 col-12">
            <label for="">O animal possui problema de saúde?</label>
            <p>
              <input @change="pet.healthProblems.error.error = false" value="1" class="with-gap" v-model="pet.healthProblems.model" name="healthProblems" type="radio" id="pros"/>
              <label for="pros">Sim</label>
              <input @change="pet.healthProblems.error.error = false" value="0" class="with-gap" v-model="pet.healthProblems.model" name="healthProblems" type="radio" id="pron"/>
              <label class="offset" for="pron">Não</label>
            </p>
            <transition name="fade" mode="out-in">
              <p v-if="pet.healthProblems.error.error" class="msg-error">{{pet.healthProblems.error.msg}}</p>
            </transition>
          </div>
        </div>
        <transition name="fade" mode="out-in">
          <div v-if="pet.healthProblems.model === '1'" class="row">
            <div class="col-md-4 col-sm-12 col-12">
              <label for="">Selecione o problema de saúde</label>
              <app-helth-input @other="toggleOther($event)" elId="helth" placeholder="Selecione" :data="pet.problems"></app-helth-input>
            </div>
            <transition name="fade" mode="out-in">
              <div v-if="other" class="col-md-8 col-sm-12 col-12">
                <label for="">Descrição</label>
                <app-text-input className="wbg" v-model="pet.otherProblems.model" :data="pet.otherProblems"></app-text-input>
              </div>
            </transition>
          </div>
        </transition>
        <div class="row">
          <div class="line"></div>
          <div class="col-md-4 col-sm-12 col-12">
            <label for="">Animal com necessidades especiais?</label>
              <p>
                <input @change="pet.specialNeeds.error.error = false" value="1" class="with-gap" v-model="pet.specialNeeds.model" name="problems" type="radio" id="esps"  />
                <label for="esps">Sim</label>
                <input @change="pet.specialNeeds.error.error = false" value="0" class="with-gap" v-model="pet.specialNeeds.model" name="problems" type="radio" id="espn"  />
                <label class="offset" for="espn">Não</label>
              </p>
               <transition name="fade" mode="out-in">
                <p v-if="pet.specialNeeds.error.error" class="msg-error">{{pet.specialNeeds.error.msg}}</p>
              </transition>
          </div>
          <div class="col-md-8 col-sm-12 col-12">
            <transition name="fade" mode="out-in">
              <span v-if="pet.specialNeeds.model === '1'">
                <label for="">Descrição</label>
                <app-text-input className="wbg" v-model="pet.specialNeedsDesc.model" :data="pet.specialNeedsDesc"></app-text-input>
              </span>
            </transition>
          </div>
          <div class="line"></div>
        </div>
        <div class="row">
          <div class="col-md-4 col-sm-12 col-12">
            <label for="">As vacinas estão em dia?</label>
              <p>
                <input @change="pet.vaccines.error.error = false" value="1" class="with-gap" v-model="pet.vaccines.model" name="vaccines" type="radio" id="vacs"  />
                <label for="vacs">Sim</label>
                <input @change="pet.vaccines.error.error = false" value="0" class="with-gap" v-model="pet.vaccines.model" name="vaccines" type="radio" id="vacn"  />
                <label class="offset" for="vacn">Não</label>
              </p>
              <transition name="fade" mode="out-in">
                <p v-if="pet.vaccines.error.error" class="msg-error">{{pet.vaccines.error.msg}}</p>
              </transition>
          </div>
          <div class="col-md-8 col-sm-12 col-12">
              <label for="">Nos envie uma imagem da carteira de vacinação</label>
              <app-file-input className="wbg" elId="vimg" v-model="pet.vaccinesImg.model" :data="pet.vaccinesImg"></app-file-input>
          </div>
          <div class="line"></div>
        </div>
        <div class="row">
          <div class="col-12 text-center">
              <button type="submit" class="btn btn-new-pet mb30">cotar</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from '@/auth'
import SelectInput from '@/components/inputs/SelectInput'
import SpecieInput from '@/components/inputs/SpecieInput'
import BreedInput from '@/components/inputs/BreedInput'
import ShapeInput from '@/components/inputs/ShapeInput'
import HelthInput from '@/components/inputs/HelthInput'
import TextInput from '@/components/inputs/TextInput'
import FileInput from '@/components/inputs/FileInput'
import EventBus from '@/main'
import QuotationHeader from '@/components/header/QuotationHeader'
export default {
  name: 'QuotationPage',
  data () {
    return {
      user: {},
      pet: {},
      other: false
    }
  },
  components: {
    appQuotationHeader: QuotationHeader,
    appTextInput: TextInput,
    appSelectInput: SelectInput,
    appSpecieInput: SpecieInput,
    appBreedInput: BreedInput,
    appShapeInput: ShapeInput,
    appHelthInput: HelthInput,
    appFileInput: FileInput
  },
  computed: {
    auth() {
      return EventBus.isAuthenticated()
    }
  },
  methods: {
    toggleOther (event){
      this.other = event
    },
    activeOther () {
      this.pet.problems.model.forEach(element => {
        if(element === 1) {
          return true
        }
      })
    },
    cleanForm () {
      this.pet = {
        birth: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        specie: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: null
        },
        breed: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: null
        },
        shape: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: null
        },
        sex: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        weight: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
        rescued: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: null
        },
        healthProblems: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: null
        },
        specialNeeds: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: null
        },
        specialNeedsDesc: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: null
        },
        vaccines: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: null
        },
        vaccinesImg: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: {}
        },
        problems: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: []
        },
        otherProblems: {
          error: {
            error: false,
            msg: 'Campo obrigatório.'
          },
          model: ''
        },
      }
    },
    validate () {
      if(this.pet.birth.model === ''){
        this.pet.birth.error.error = true
      }
      else if(this.pet.birth.model.length < 10){
        this.pet.birth.error.error = true
        this.pet.birth.error.msg = 'Data inválida'
      }
      if(this.pet.specie.model === null){
        this.pet.specie.error.error = true
      }
      if(this.pet.breed.model === null){
        this.pet.breed.error.error = true
      }
      if(this.pet.shape.model === null){
        this.pet.shape.error.error = true
      }
      if(this.pet.sex.model === ''){
        this.pet.sex.error.error = true
      }
      if(this.pet.weight.model === ''){
        this.pet.weight.error.error = true
      }
      if(this.pet.rescued.model === null){
        this.pet.rescued.error.error = true
      }
      if(this.pet.healthProblems.model === null){
        this.pet.healthProblems.error.error = true
      }
      if(this.pet.specialNeeds.model === null){
        this.pet.specialNeeds.error.error = true
      }
      if(this.pet.vaccines.model === null){
        this.pet.vaccines.error.error = true
      }
      let error = false
      for(let key in this.pet){
        const element = this.pet[key]
        if(element.error.error){
          error = true
        }
      }
      return error
    },
    submit () {
      const error = this.validate()
      if(!error){
        const formData = {
          especie_id: this.pet.specie.model,
          raca_id: this.pet.breed.model,
          sexo: this.pet.sex.model,
          data_nascimento: this.pet.birth.model,
          resgatado: this.pet.rescued.model,
          vacinacao_emdia: this.pet.vaccines.model,
          problema_saude: this.pet.healthProblems.model,
          peso: this.pet.weight.model,
          porte_id: this.pet.shape.model,
          necessidade_especial: this.pet.specialNeeds.model,
          problemasId: this.pet.problems.model,
          problema_outro: this.pet.otherProblems.model,
          descricao_necessidade: this.pet.specialNeedsDesc.model,
          carteira_vacinacao: this.pet.vaccinesImg.model
        }
        axios.post('/cotacao', formData)
        .then(res => {
          const quotation = res.data
          this.$router.push({name: 'PlanQuotationPage', params: {firstStepRegister: formData, quotation: quotation}})
        })
        .catch(error => {
          console.log(error)
        })
      }
    }
  },
  created () {
    this.cleanForm()
    if(EventBus.isAuthenticated()){
      this.$store.dispatch('fetchUser').then(() =>{
        this.user = JSON.parse(localStorage.getItem('user'))
        EventBus.$emit('loadingPageCallback', false)
     })
    }
    else{
      EventBus.$emit('loadingPageCallback', false)
    }
  }
}
</script>

<style scoped>

  .page{
    min-height: 100vh;
  }

  .my-container {
    max-width: 950px;
    padding-top: 150px;
  }

  .title{
    color: #FFD26C;
    font-family: ubuntu-medium;
    font-size: 20px;
    margin-bottom: 50px;
  }

  .line{
    width: 100%;
    height: 1px;
    background-color: rgba(255,255,255,.15);
    margin-top: 10px;
    margin-bottom: 20px;
    display: block;
  }

  .offset{
    margin-left: 50px;
  }

  label{
    color: #fff !important;
  }

  .msg-error{
    color: #E60965;
    font-family: Lato-Light;
    font-size: 9px;
    margin-top: -10px;
  }

  @media only screen and (max-width: 767.98px){
    .title{
      font-size: 17px;
      text-align: center;
    }

    label{
      font-size: 12px;
    }
  }
</style>



