<template>
    <div class="page">
      <app-client-header></app-client-header>

      <section class="how-works">
        <div class="container">
          <div class="row">
            <div class="col text-center">
              <h2>Como funciona o plano?</h2>
            </div>
          </div>
          <div class="row">
            <img src="static/imgs/c_line.svg" alt="" class="line">
            <div class="col-md col-sm-12 col-12 text-center">
              <div class="item">
                <img src="static/imgs/c_one.svg" alt="">
                <p class="desc">Escolha seu veterinário.</p>
              </div>
            </div>
            <div class="col-md col-sm-12 col-12 text-center">
              <div class="item">
                <img src="static/imgs/c_two.svg" alt="">
                <p class="desc">Contate o plano e informe o telefone
                    e o endereo do médico/clínica.</p>
              </div>
            </div>
            <div class="col-md col-sm-12 col-12 text-center">
              <div class="item">
                <img src="static/imgs/c_three.svg" alt="">
                <p class="desc">Aguarde a liberação e após liberado,
                    a Iopet cobrirá os gastos de sua
                    consulta.
                </p>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col text-right">
              <div class="emergency-box text-center">
                <div class="quiet">
                  <p>Fique tranquilo!</p>
                </div>
                <p>Em casos de emergência o plano poderá ser acionado após a consulta ou procedimento.</p>
              </div>
            </div>
            <img src="static/imgs/dog.png" alt="" class="dog">
          </div>
        </div>
      </section>

      <div class="blue text-center">
        <h2>Resumo do Pet</h2>
      </div>

      <section class="pets">
        <div class="container">
          <div class="row justify-content-center">
            <div v-for="(item, index) in pets" :key="index" class="col-md-4 col-sm-6 col-12">
              <app-pet :index="index" :pet="item"></app-pet>
            </div>
          </div>
        </div>
      </section>

      <div class="blue text-center">
        <router-link to="/cadastro-cotacao">
          <button class="btn btn-new-pet">ADICIONAR NOVO PET</button>
        </router-link>
      </div>

      <section class="monthly">
        <div class="container">
          <div class="row text-center">
            <div class="col">
              <h2>Mensalidade</h2>
            </div>
          </div>
          <div class="row">
            <div class="col">
              <div class="month">
                <div class="row">
                  <div class="col-md col-sm-12">
                    <p class="date">
                      {{competence(this.monthly.maturity)}} <br>
                      Vencimento: {{dateFormat(this.monthly.maturity)}}
                    </p>
                  </div>
                  <div class="col-md col-sm-12">
                    <p class="value">R$ {{this.monthly.value}}</p>
                  </div>
                  <div class="col-md col-sm-12">
                    <p class="status">Pendente</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col text-center">
              <router-link to="/financeiro">
                <p class="show-all">Ver todo o financeiro</p>
              </router-link>
            </div>
          </div>
        </div>
      </section>

      <app-footer></app-footer>
    </div>
</template>

<script>
import axios from '@/auth'
import EventBus from '@/main'
import Pet from '@/components/pets/Pet'
import Footer from '@/components/footer/Footer'
import ClientHeader from '@/components/header/ClientHeader'
export default {
  name: 'ClientPage',
  data () {
    return {
      user: {},
      pets: [],
      monthly: {}
    }
  },
  components: {
    appClientHeader: ClientHeader,
    appFooter: Footer,
    appPet: Pet
  },
  created () {
    this.$store.dispatch('fetchUser').then(() =>{
      this.user = JSON.parse(localStorage.getItem('user'))
      axios.get('/pets')
      .then(res => {
        const data = res.data.pets
        const pets = []
        for(let key in data){
          const pet = {
            id: data[key].id,
            name: data[key].nome,
            age: data[key].idade,
            img: data[key].imagem,
            breed: data[key].raca_nome.nome
          }
          pets.push(pet)
        }
        this.pets = pets

        axios.get('/fatura')
        .then(res => {
          const data = res.data.Fatura
          const monthly = {
            value: data.valor,
            maturity: data.vencimento
          }
          this.monthly = monthly
          EventBus.$emit('loadingPageCallback', false)
        })
        .catch(error => {
          console.log(error)
        })
      })
      .catch(error => {
        console.log(error)
      })

    })
  }
}
</script>

<style scoped>

  .page{
    margin-top: 120px;
  }

  .how-works{
    position: relative;
    width: 100%;
    min-height: 600px;
    height: auto;
    background-color: #fff;
    background-image: url(/static/imgs/bg1_reg.png);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    padding-bottom: 120px;
  }

  .how-works h2{
    color: #1E62AC;
    font-size: 30px;
    font-family: ubuntu-medium;
    margin-top: 50px;
    margin-bottom: 80px;
  }

  .how-works .line{
    position: absolute;
    left: 50%;
    top: 250px;
    width: 800px;
    transform: translate(-50%);
    -moz-transform: translate(-50%);
    -webkit-transform: translate(-50%);
    -ms-transform: translate(-50%);
    -o-transform: translate(-50%);
  }

  .how-works .item{
    display: inline;
    color: #676767;
    font-size: 16px;
    font-family: Lato-Light;
    text-align: center;
  }

  .how-works .item img{
    margin-bottom: 35px;
    height: 180px;
    width: 180px;
  }

  .how-works .emergency-box{
    width: 630px;
    height: 80px;
    background-color: #E60965;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    font-size: 15px;
    color: #fff;
    display: inline-block;
    line-height: 100px;
    margin-top: 90px;
    position: relative;
  }

  .how-works .emergency-box .quiet{
    width: 265px;
    height: 60px;
    line-height: 60px;
    background-color: #FFD26C;
    color: #1E62AC;
    font-size: 28px;
    font-family: ubuntu-medium;
    display: block;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
  }

  .dog{
    position: absolute;
    left: 0;
    bottom: -1px;
  }

  .blue{
    width: 100%;
    height: auto;
    padding-top: 50px;
    padding-bottom: 50px;
    background-color: #1E62AC;
  }

  .blue h2{
    color: #fff;
    font-size: 30px;
    font-family: ubuntu-medium;
    margin-bottom: 0;
  }

  .blue .btn{
    margin-top: -30px;
  }

  .pets{
    width: 100%;
    height: auto;
    background-color: #fff;
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .monthly{
    width: 100%;
    height: auto;
    background-color: #fff;
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .monthly h2{
    color: #0B2027;
    font-size: 30px;
    font-family: ubuntu-medium;
    margin-bottom: 60px;
  }

  .monthly .month{
    width: 100%;
    max-width: 605px;
    height: auto;
    min-height: 80px;
    border-radius: 46px;
    background-color: #7F378A;
    display: block;
    margin: 0 auto;
  }

  .monthly .month .date{
    color: rgba(255, 255, 255, 0.8);
    position: relative;
    top: 60%;
    transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    padding-left: 20px;
    line-height: 16px;
  }

  .monthly .month .value{
    font-family: ubuntu-bold;
    font-size: 34px;
    color: #fff;
    position: relative;
    top: 60%;
    transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    text-align: center;
  }

  .monthly .month .status{
    font-size: 16px;
    font-family: Lato-bold;
    color: #FFD26C;
    text-align: right;
    position: relative;
    top: 60%;
    transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    padding-right: 35px;
  }

  .show-all{
    color: #E60965;
    font-family: Lato-bold;
    margin-top: 25px;
    cursor: pointer;
  }

  @media only screen and (max-width: 992px){

    .how-works h2{
      font-size: 26px;
    }

    .how-works .item img{
      height: 130px;
      width: 130px;
    }

    .how-works .line{
      width: 560px;
      top: 230px;
    }

    .how-works .desc{
      font-size: 13px;
    }

    .how-works .emergency-box .quiet{
      width: 170px;
      height: 38px;
      font-size: 18px;
      line-height: 36px;
    }

    .how-works .emergency-box{
      width: 480px;
      font-size: 12px;
      height: 55px;
      line-height: 70px;
    }

    .blue h2, .monthly h2{
      font-size: 26px;
    }

    .blue{
      padding-top: 30px;
      padding-bottom: 30px;
    }

    .dog{
      height: 260px;
      width: auto;
    }
  }

  @media only screen and (max-width: 768.98px){
    .how-works h2{
      font-size: 17px;
    }

    .how-works .line{
      display: none;
    }

    .how-works .desc{
      margin-bottom: 30px;
    }

    .how-works .emergency-box{
      width: 100%;
      height: auto;
      line-height: 20px;
      padding-top: 30px;
    }

    .how-works{
      padding-bottom: 220px;
    }

    .dog{
      left: 68%;
      transform: translateX(-50%);
      -moz-transform: translateX(-50%);
      -webkit-transform: translateX(-50%);
      -o-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
    }

    .blue h2,
    .monthly h2{
      font-size: 17px;
    }

    .monthly .month .date,
    .monthly .month .status{
      text-align: center;
      transform: none;
      margin: 0;
      padding: 0;
    }

    .monthly .month{
      max-height: none;
      padding-bottom: 30px;

    }
  }
</style>



