<template>
  <span>
    <div v-if="type==='card'" class="item card">
      <div @click="details" class="card-content">
        <transition name="fade" mode="out-in">
          <img v-if="card.active===1" src="static/imgs/gcheck.svg" alt="" class="active">
        </transition>
        <div class="row">
          <div class="col-auto">
            <img class="chip" src="static/imgs/chip.svg" alt="">
          </div>
          <div class="col text-right">
            <img v-if="card.flag==='AMEX'" class="amex" src="static/imgs/american.svg" alt="">
            <img v-if="card.flag==='Visa'" class="visa" src="static/imgs/visa.svg" alt="">
            <img v-if="card.flag==='Mastercard'" class="master" src="static/imgs/mastercard.svg" alt="">
            <img v-if="card.flag==='Diners'" class="diners" src="static/imgs/diners.svg" alt="">
            <img v-if="card.flag==='Elo'" class="elo" src="static/imgs/elo.svg" alt="">
            <img v-if="card.flag==='Hiper'" class="hiper" src="static/imgs/hiper.svg" alt="">
            <img v-if="card.flag==='Hiper-card'" class="hiperc" src="static/imgs/hipercard.svg" alt="">
          </div>
        </div>
        <p v-if="card.number" class="card-number">XXXX-XXXX-XXXX-{{card.number}}</p>
        <p v-if="card.status==='invalido'" class="invalid">Cartão inválido</p>
      </div>
      <p v-if="!add" @click="setMainCard" :class="{block: card.status==='invalido' || card.active===1}" class="pattern">Escolher como padrão</p>
    </div>

    <div v-if="type==='new'" class="item new">
      <div class="new-content" @click="newCard" v-if="type==='new'">
        <span class="center text-center">
          <img src="static/imgs/plus.svg" alt="" class="plus">
          <p>Adicionar um cartão</p>
        </span>
      </div>
    </div>

  </span>
</template>

<script>
export default {
  name: 'Card',
  data () {
    return {

    }
  },
  props: [
    'card',
    // flag: AMEX, Visa, Mastercard, Diners, Elo, Hiper, Hiper-card
    // number final card numbers
    // active: main card 0,1
    // status: invalido, aceito
    'type', // type: card, new
    'add' // true, false
  ],
  methods: {
    setMainCard() {
      if(this.card.status != 'invalido' && this.card.active !=1){
        this.$emit('selectCard')
      }
    },
    newCard () {
      this.$emit('newCard')
    },
    details () {
      this.$emit('details')
    }
  }
}
</script>

<style scoped>
  .item{
    width: 100%;
    height: 140px;
    border-radius: 7px;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    margin-bottom: 70px;
    max-width: 220px;
    position: relative;
    cursor: pointer;
    display: inline-block;
  }

  .item.card{
    background-color: #fff;
    padding: 30px 20px;
  }

  .item.new{
    background-color: #8B93A6;
  }

  .chip{
    height: 23px;
    width: auto;
  }

  .active{
    height: 26px;
    width: auto;
    position: absolute;
    left: 0;
    top: 0;
    transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
  }

  .pattern{
    color: #E60965;
    font-size: 11px;
    text-align: center;
    position: absolute;
    left: 50%;
    bottom: -50px;
    transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    cursor: pointer;
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    -ms-transition: all .3s;
  }

  .pattern.block{
    cursor: no-drop;
    color: rgba(230, 9, 101, 0.3);
  }

  .invalid{
    color: #EB001B;
    font-size: 10px;
    position: absolute;
    bottom: -7px;
    left: 50%;
    transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
  }

  .center{
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
  }

  .center img{
    height: 40px;
    width: auto;
    display: block;
    margin: 0 auto;
  }

  .center p{
    margin-top: 20px;
    color: #C5C9D2;
    font-size: 12px;
  }

  .new-content, .card-content{
    width: 100%;
    height: 100%;
    display: block;
  }

  .card-number{
    position: absolute;
    bottom: 10px;
  }

  @media only screen and (max-width: 575.98px){
    .item{
      display: block;
      margin-right: auto;
      margin-left: auto;
    }
  }
</style>



