<template>
  <div class="page">
    <img src="static/imgs/bg_login.png" alt="" class="bg desk">
    <router-link to="/">
      <img src="static/imgs/logo1.svg" alt="" class="logo">
    </router-link>
    <div class="container-fluid my-container">
      <div class="box">
        <div class="row">
          <div class="col">
            <p class="welcome">Bem-vindo, faça login e aproveite todas as vantagens de nossos planos.</p>
          </div>
        </div>
        <form @submit.prevent="login">
          <div class="row">
            <div class="col-md col-sm-12 col-12">
              <app-email-input
                className="wbg"
                :data="email"
                placeholder="E-mail"
                v-model="email.model">
              </app-email-input>
            </div>
            <div class="col-md col-sm-12 col-12">
              <app-password-input
                className="wbg"
                :data="password"
                placeholder="Senha"
                v-model="password.model">
              </app-password-input>
            </div>
          </div>
          <div class="row mt15">
            <div class="col-md col-sm-12 col-12">
              <button type="submit" class="btn btn-enter">entrar</button>
            </div>
            <div class="col-md col-sm-12 col-12">
              <ul class="list-inline list-unstyled text-center">
                <li class="list-inline-item">
                  <router-link to="/esqueci-minha-senha">Esqueci minha senha</router-link>
                </li>
                <li class="list-inline-item">
                  <router-link to="/cadastro-cotacao">Não tenho uma conta</router-link>
                </li>
              </ul>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import EmailInput from '@/components/inputs/EmailInput'
import PasswordInput from '@/components/inputs/PasswordInput'
import EventBus from '@/main'
import axios from 'axios'
export default {
  name: 'LoginPage',
  data () {
    return {
      email: {
        error: {
          error: false,
          msg: 'E-mail incorreto'
        },
        model: ''
      },
      password: {
        error: {
          error: false,
          msg: 'Senha incorreta'
        },
        model: ''
      }
    }
  },
  components: {
    appEmailInput: EmailInput,
    appPasswordInput: PasswordInput
  },
  methods: {
    login () {
      const formData = {
        email: this.email.model,
        password: this.password.model,
      }
      this.$store.dispatch('login', formData)
    }
  },
  created () {
    EventBus.$emit('loadingPageCallback', false)

    if(this.$store.getters.isAuthenticated){
      this.$router.push({name: 'ClientPage'})
    }

    EventBus.$on('emailError', status => {
      this.email.error.error = status
    })

    EventBus.$on('passwordError', status => {
      this.password.error.error = status
    })
  },
  mounted() {
    EventBus.$emit('loadingPageCallback', false)
  }

}
</script>

<style scoped>
  .page{
    min-height: 100vh;
    position: relative;
    background-color: #fff;
  }

  .bg{
    position: absolute;
    bottom: 0;
    right: 0;
    height: 100vh;
    width: auto;
  }

  .logo{
    position: fixed;
    height: 60px;
    width: auto;
    top: 80px;
    left: 60px;
    cursor: pointer;
    z-index: 2;
  }

  .box{
    width: 100%;
    max-width: 515px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
  }

  .box .welcome{
    color: #676767;
    font-family: ubuntu-regular;
    font-size: 16px;
  }

  .box ul li{
    color: #E60965;
    font-family: Lato-Light;
    font-size: 12px;
    cursor: pointer;
  }

  .box ul li:first-child{
    border-right: 1px solid #E60965;
    padding-right: 10px;
  }

  @media only screen and (max-width: 992px){
    .box{
      left: 50%;
      transform: translateX(-50%);
      -moz-transform: translateX(-50%);
      -webkit-transform: translateX(-50%);
      -o-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
    }
  }

  @media only screen and (max-width: 767.98px){
    .box{
      transform: none;
      position: static;
      display: block;
      margin: 30vh auto 0 auto;
    }

    .logo{
      left: 50%;
      transform: translateX(-50%);
      -moz-transform: translateX(-50%);
      -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
      -o-transform: translateX(-50%);
    }

    .page{
      min-height: auto;
    }

    .btn.btn-enter, .btn.btn-send{
      margin-bottom: 30px;
    }
  }
</style>



