<template>
  <div class="page">
    <app-client-header></app-client-header>

    <div v-if="overlay" class="overlay"></div>
    <transition name="fade" mode="out-in">
      <div v-if="modalConf" class="my-modal conf">
        <div class="row">
          <div class="col text-right">
            <div class="emergency-box text-center">
              <div class="quiet">
                <p>Atenção!</p>
              </div>
              <p class="text">Todos os dados deste animal serão perdidos.</p>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col">
            <p class="desc">
              Seu plano atual é de <span>R$ {{numberFormat(this.planValue/100,2,',','.')}}</span> <br>
              após cancelar este animal,  o valor passará a ser de <span>R$ {{numberFormat((this.planValue-this.pet.plan)/100,2,',','.')}}</span>
            </p>
          </div>
        </div>
        <div class="row">
          <div class="col text-center">
            <button @click="modalConf = false, overlay = false" class="btn keep">manter no plano</button>
            <button @click="confirmation()" class="btn keep">cancelar animal</button>
          </div>
        </div>
      </div>
    </transition>

    <transition name="fade" mode="out-in">
      <div v-if="modal" class="my-modal text-center">
        <p class="title">Seu plano foi cancelado com sucesso.</p>
        <p class="msg">{{modalMsg}}</p>
        <button @click="replace()" class="btn btn-black">ok</button>
      </div>
    </transition>

    <div class="container-fluid my-container">
      <div class="row">
        <div class="col-sm col-12">
          <img class="dog" src="static/imgs/boxer_dpression.png" alt="">
        </div>
        <div class="col-sm col-12 mt30">
          <p class="title">Deseja mesmo cancelar o plano deste pet?</p>
          <p class="desc"><span class="pet">{{pet.name}}</span> não mais será assegurado pelo Iopet caso você continue. <br>
            Nos diga o motivo do cancelamento.</p>
          <form @submit.prevent="submit()">
            <p>
              <input value="1" v-model="reason" class="with-gap" name="motivo" type="radio" id="alto"  />
              <label for="alto">Alto custo</label>
            </p>
            <p>
              <input value="2" v-model="reason" class="with-gap" name="motivo" type="radio" id="falecimento"  />
              <label for="falecimento">Falecimento</label>
            </p>
            <p>
              <input value="3" v-model="reason" class="with-gap" name="motivo" type="radio" id="mudanca"  />
              <label for="mudanca">Mudança de plano</label>
            </p>
            <p>
              <input value="4" v-model="reason" class="with-gap" name="motivo" type="radio" id="outro"  />
              <label for="outro">Outro</label>
            </p>
            <transition name="fade" mode="out-in">
              <textarea v-if="reason == 4" v-model="textarea" class="wbg" name="" id="" cols="30" rows="5"></textarea>
            </transition>
            <button type="submit" class="btn btn-send mt15  ">remover pet do plano</button>
          </form>
        </div>
      </div>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import axios from '@/auth'
import EventBus from '@/main'
import Footer from '@/components/footer/Footer'
import ClientHeader from '@/components/header/ClientHeader'
export default {
  name: 'PetCancellationPage',
  data () {
    return {
      user: {},
      modalMsg: '',
      reason: '',
      textarea: '',
      modalConf: false,
      modal: false,
      overlay: false,
      pet: {},
      planValue: null
    }
  },
  components: {
    appFooter: Footer,
    appClientHeader: ClientHeader,
  },
  methods: {
    submit () {
      if(this.reason === ''){
      }
      else{
        this.overlay = true
        this.modalConf = true
      }
    },
    confirmation () {
      if(this.reason == 4){
        const formData = {
          animal_id: this.pet.id,
          motivo: this.reason,
          descricao: this.textarea
        }
        this.send(formData)
      }
      else{
        const formData = {
          animal_id: this.pet.id,
          motivo: this.reason
        }
        this.send(formData)
      }
    },
    replace () {
      this.$router.push({name: 'ClientPage'})
    },
    send (formData) {
      axios.post('/cancelar/plano', formData)
      .then(res => {
        console.log(res)
        this.modalConf = false
        if(this.reason == 1){
          this.modalMsg = 'Sentimos em saber que o plano está acima da sua expectativa. \n Manteremos contato e enviaremos em breve, para seu e-mail, \n novidades e promoções.'
        }
        else if(this.reason == 2){
          this.modalMsg = 'Lamentamos muito pela sua perda. \n Temos certeza que seu animal teve uma vida feliz ao seu lado.'
        }
        else if(this.reason == 3){
          this.modalMsg = 'Você vai embora? Sentimos muito pela sua partida! \n Sentiremos a sua falta. Esperamos rever você em breve.'
        }
        else if(this.reason == 4){
          this.modalMsg = 'Lamentamos pela sua decisão, esperamos que retorne em breve.'
        }
        this.modal = true
      })
      .catch(error => {
        console.log(error)
      })
    }
  },
  created () {
    this.$store.dispatch('fetchUser').then(() =>{
      this.user = JSON.parse(localStorage.getItem('user'))
      this.planValue = this.user.plano.valor_total
      const petId = this.$route.params.id
      axios.get('/resumo/pet/'+petId)
      .then(res => {
        const data = res.data.pet
        const pet = {
          id: data.id,
          name: data.nome,
          plan: data.plano.valor
        }
        this.pet = pet
        EventBus.$emit('loadingPageCallback', false)
      })
      .catch(error => {
        console.log(error)
      })
    })
  }
}
</script>

<style scoped>
  .page{
    margin-top: 120px;
  }

  .container-fluid{
    min-height: 100vh;
  }

  .title{
    color: #E60965;
    font-size: 20px;
    font-family: ubuntu-medium;
  }

  .desc{
    color: #000;
    font-family: Lato-bold;
  }

  .pet{
    font-family: ubuntu-medium;
    font-size: 16px;
    color: #1E62AC;
  }

  form p{
    margin-bottom: 0;
  }

  form .btn{
    width: auto;
  }

  .my-modal.conf{
    width: 510px;
  }

  .my-modal.conf .desc{
    color: #707070;
    font-family: ubuntu-regular;
  }

  .my-modal.conf .desc span{
    color: #1E62AC;
  }

  .emergency-box{
    width: 100%;
    height: 60px;
    background-color: #E60965;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
    font-size: 15px;
    color: #fff;
    display: inline-block;
    position: relative;
    margin-bottom: 30px;
  }

  .emergency-box .text{
    margin-top: 30px;
  }

  .emergency-box .quiet{
    width: 185px;
    height: 40px;
    line-height: 40px;
    background-color: #FFD26C;
    color: #1E62AC;
    font-size: 18px;
    font-family: ubuntu-medium;
    display: block;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
  }

  .btn.keep{
    background-color: #0B2027;
    border-radius: 5px;
    font-family: Lato-bold;
    font-size: 10px;
    height: 30px;
    width: 122px;
    color: #fff;
    text-transform: uppercase;
    margin: 5px;
  }

  .my-modal .title{
    color: #E60965;
    font-size: 18px;
    font-family: ubuntu-medium;
    text-align: center;
  }

  .my-modal .msg{
    color: #505050;
    text-align: center;
    white-space: pre-wrap;
    font-family: ubuntu-regular;
  }

  .my-modal .btn-black{
    width: 120px;
    margin-top: 30px;
  }

  @media only screen and (max-width: 992px){
    .dog{
      width: 100%;
    }

    .title{
      font-size: 16px;
    }

    .desc{
      font-size: 10px;
    }

    .pet{
      font-size: 11px;
    }
  }

  @media only screen and (max-width: 767.98px){
    .emergency-box{
      height: auto;
    }

    .container-fluid{
      padding-bottom: 30px;
    }
  }
</style>



