import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import axios from 'axios'
import {store} from './store'
import {routes} from './routes'
import instance from  './auth'

Vue.config.productionTip = false

Vue.use(VueRouter)
export const router = new VueRouter({
  routes,
  scrollBehavior (to, from, savedPosition) {
    EventBus.$emit('loadingPageCallback', true)
    return {x:0,y:0}
  }
})

const EventBus = new Vue({
  methods: {
    isAuthenticated () {
      return localStorage.getItem('token') !== null
    }
  }
})
export default EventBus

Vue.mixin({
  methods: {
    back(){
      this.$router.go(-1)
    },
    logout () {
      this.$store.dispatch('logout')
    },
    competence (date) {
      if(date) {
        var formDate = date.split('-')
        if(formDate[1] === '01'){
          return 'Dezembro/'+formDate[0]
        }
        if(formDate[1] === '02'){
          return 'Janeiro/'+formDate[0]
        }
        if(formDate[1] === '03'){
          return 'Fevereiro/'+formDate[0]
        }
        if(formDate[1] === '04'){
          return 'Março/'+formDate[0]
        }
        if(formDate[1] === '05'){
          return 'Abril/'+formDate[0]
        }
        if(formDate[1] === '06'){
          return 'Maio/'+formDate[0]
        }
        if(formDate[1] === '07'){
          return 'Junho/'+formDate[0]
        }
        if(formDate[1] === '08'){
          return 'Julho/'+formDate[0]
        }
        if(formDate[1] === '09'){
          return 'Agosto/'+formDate[0]
        }
        if(formDate[1] === '10'){
          return 'Setembro/'+formDate[0]
        }
        if(formDate[1] === '11'){
          return 'Outubro/'+formDate[0]
        }
        if(formDate[1] === '12'){
          return 'Novembro/'+formDate[0]
        }
      }
    },
    dateFormat (date){
      if(date) {
        var formDate = date.split('-')
        return formDate[2]+'/'+formDate[1]+'/'+formDate[0]
      }
    },
    numberFormat (number, decimals, dec_point, thousands_sep) {
      var n = !isFinite(+number) ? 0 : +number,
          prec = !isFinite(+decimals) ? 0 : Math.abs(decimals),
          sep = (typeof thousands_sep === 'undefined') ? ',' : thousands_sep,
          dec = (typeof dec_point === 'undefined') ? '.' : dec_point,
          toFixedFix = function (n, prec) {
              var k = Math.pow(10, prec)
              return Math.round(n * k) / k
          },
          s = (prec ? toFixedFix(n, prec) : Math.round(n)).toString().split('.')
      if (s[0].length > 3) {
          s[0] = s[0].replace(/\B(?=(?:\d{3})+(?!\d))/g, sep)
      }
      if ((s[1] || '').length < prec) {
          s[1] = s[1] || ''
          s[1] += new Array(prec - s[1].length + 1).join('0')
      }
      return s.join(dec)
    }
  }
})

axios.defaults.baseURL = 'http://13.59.1.72/iopet/lumenapiiopet/public'
axios.defaults.headers.common['Accept'] = 'application/json'
axios.defaults.headers.common['Content-Type'] = 'application/json'
instance.interceptors.response.use(undefined, function (error) {
  if(error.response.status === 400 || error.response.status === 401) {
    store.dispatch('logout')
    router.push({ name: 'LoginPage' })
  }
  throw(error)
})
axios.interceptors.response.use(undefined, function (error) {
  if(error.response.status === 400 || error.response.status === 401) {
    store.dispatch('logout')
    router.push({ name: 'LoginPage' })
  }
  throw(error)
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router,
  store
})



