<template>
  <div id="app">
    <transition name="fade" mode="out-in">
      <app-loading-page v-if="loadingPage"></app-loading-page>
    </transition>
    <main :class="{blue: blue, blur: loadingPage}">
      <transition name="fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </main>
  </div>
</template>

<script>
import LoadingPage from '@/components/loading/LoadingPage'
import EventBus from '@/main'
export default {
  name: 'App',
  data () {
    return {
      blue: false,
      loadingPage: false
    }
  },
  components: {
    appLoadingPage: LoadingPage
  },
  methods: {
  },
  created () {
    this.$store.dispatch('tryAutoLogin')
    EventBus.$on('loadingPageCallback', status => {
      this.loadingPage = status
    })
  },
  mounted () {
    if((this.$route.name == 'PlanQuotationPage') || this.$route.name == 'QuotationPage'){
      this.blue = true
    }
    else{
      this.blue = false
    }
  },
  watch: {
    $route(to, from){
      if((this.$route.name == 'PlanQuotationPage') || this.$route.name == 'QuotationPage'){
        this.blue = true
      }
      else{
        this.blue = false
      }
    }
  }
}
</script>

