  }

  main.blue{
    background-color: #609BDB;
    background-image: url(/static/imgs/bg_reg.png);
  }


  .page{
    width: 100%;
    height: auto;
    position: relative;
  }

  .my-container{
    max-width: 1280px;
  }

  /*MODAL*/
  .my-modal{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
    background-color: #fff;
    padding: 45px 45px 30px 45px;
    z-index: 999;
    display: block;
    max-width: calc(100% - 30px);
    max-height: 90vh;
    overflow-y: auto;
    width: auto;
    height: auto;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                        0 1px 5px 0 rgba(0, 0, 0, 0.12),
                        0 3px 1px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
                0 1px 5px 0 rgba(0, 0, 0, 0.12),
                0 3px 1px -2px rgba(0, 0, 0, 0.2);
  }

  .my-modal .closes{
    position: absolute;
    right: 15px;
    top: 15px;
    height: 17px;
    width: auto;
    cursor: pointer;
  }

  .my-modal .line{
    width: 100%;
    height: 1px;
    background-color: rgba(11, 32, 39, 0.15);
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .overlay{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 988;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
  }

  .click{
    cursor: pointer;
  }

  .mob{
    display: none;
  }

  @media only screen and (max-width: 992px) {
    .mob{
      display: block;
    }

    .desk{
      display: none;
    }

    .btn.btn-register{
      font-size: 10px;
      width: 86px;
      height: 30px;
    }

    .btn.btn-price{
      width: 115px;
      height: 45px;
      font-size: 10px;
      line-height: 15px;
    }

    .btn.btn-price span{
      font-size: 14px;
    }

    [type="radio"]:not(:checked) + label, [type="radio"]:checked + label{
      font-size: 10px;
    }
  }

  @media only screen and (max-width: 767.98px){
    .my-select ul li{
      font-size: 12px !important;
    }
  }
</style>



